import React, { Component } from 'react'
import "../css/news.css"
import axios from "axios"
import { Link, withRouter } from 'react-router-dom'
import Pagination from '@material-ui/lab/Pagination'
import urls from '../urls.json'
import Auth from './Authentication/auth'

const PROFILE_URL = urls.BASE_URL + urls.PROFILE
const NEWS_URL = urls.BASE_URL + urls.NEWS

class News extends Component {
    constructor(props) {
        super(props);
        this.state = {
            news_list: [],
            total_pages: 1,
            page_num: 1
        }
        this.reload = this.reload.bind(this)
        this.showNews = this.showNews.bind(this)
        this.changePagination = this.changePagination.bind(this)
    }

    async componentDidMount() {
        localStorage.setItem('nKey', 1)
        this.reload()
    }

    async reload() {
        await axios.get(NEWS_URL).then(res => {
            this.setState({
                news_list: res.data.topics,
                total_pages: Math.ceil(Number(res.data.topics.length) / 8)
            })
        }).catch(err => {
            console.log(err)
        })

        await axios.post(PROFILE_URL + "&token=" + Auth.getToken()).then(async (res) => {
            if ((res.data.type === "needlogin" || res.data.type === "fail") && Auth.isUserAuthenticated()) {
                alert("您的登录状态无效，请重新登录。")
                Auth.deauthenticateUser()
                window.location.reload()
            }
        }).catch(err => {
            console.log(err)
        })
    }

    componentWillUnmount = () => {
        this.setState = (state, callback) => {
            return;
        };
    }

    async changePagination(event, page_num) {
        await this.setState({ page_num: page_num })
    }

    showNews() {
        if (this.state.news_list.length === 0) return null
        var newslist = [], end = (8 * this.state.page_num) > this.state.news_list.length ? this.state.news_list.length : (8 * this.state.page_num)
        for (let i = 8 * (this.state.page_num - 1); i < end; i++) {
            newslist.push(
                <li key={i}>
                    <em>
                        <Link to={`/news_detail/${this.state.news_list[i].id}`}>{this.state.news_list[i].Title}</Link>
                    </em>
                    <span>{this.state.news_list[i].CDate}</span>
                </li>
            )
        }
        return newslist
    }

    render() {
        return (
            <div className="containerwidth newspage">
                <div className="l_Center news l_Clearfix">
                    <div className="news-main">
                        <div className="news_list">
                            <h2>新闻公告</h2>
                            <div className="list">
                                <ul id="newslist">
                                    {this.showNews()}
                                </ul>
                            </div>
                            <div className="space3"></div>
                            <div className="news_pagination">
                                <Pagination count={this.state.total_pages} color="primary" variant="outlined" shape="rounded" onChange={this.changePagination} />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default withRouter(News)