import React, { Component } from "react";
import './index.css'
import Auth from '../../Authentication/auth'
import urls from '../../../urls.json'
// import { Table } from "@material-ui/core";
import axios from 'axios'

const PROFIT_STATISTICS = urls.BASE_URL + urls.PROFIT_STATISTICS

class Profit_statistics extends Component {
    constructor(props) {
        super(props)
        this.state = {
            profit_data: []
        }
        this.showThead = this.showThead.bind(this)
        this.showTbody = this.showTbody.bind(this)
    }
    componentDidMount() {
        this.Profit_data()
    }

    componentWillUnmount = () => {
        this.setState = (state, callback) => {
            return;
        };
    }

    async Profit_data() {
        await axios.get(PROFIT_STATISTICS + "&token=" + Auth.getToken()).then(res => {
            if (res.data.type === "success") {
                this.setState({ profit_data: res.data.topics })
            }
        }).catch(err => {
            console.log(err)
        })
    }

    showThead() {
        if (this.state.profit_data.length === 0) return null
        var thead = []
        thead.push(<th key={0}>游戏名称</th>)
        for (let i = 0; i < this.state.profit_data[0].Days.length; i++) {
            thead.push(<th key={i + 1}>{this.state.profit_data[0].Days[i]}</th>)
        }
        thead.push(<th key={this.state.profit_data[0].Days.length + 1}>总数</th>)
        return <tr>{thead}</tr>
    }

    showTbody() {
        if (this.state.profit_data.length === 0) return null
        var tbody = [], key = 0, sum = [], totalsum = 0
        for (let i = 0; i < this.state.profit_data[0].Profits.length; i++) sum[i] = 0
        for (let i = 0; i < this.state.profit_data.length; i++) {
            var record = []
            record.push(<td key={key++}>{this.state.profit_data[i].Name}</td>)
            for (let j = 0; j < this.state.profit_data[i].Profits.length; j++) {
                record.push(<td key={key++}>{this.state.profit_data[i].Profits[j]}</td>)
                sum[j] += Number(this.state.profit_data[i].Profits[j])
            }
            record.push(<td key={key++}>{this.state.profit_data[i].SumProfit}</td>)
            totalsum += Number(this.state.profit_data[i].SumProfit)
            tbody.push(<tr key={i}>{record}</tr>)
        }

        record = []
        record.push(<td key={key++}>总数</td>)
        for (let i = 0; i < this.state.profit_data[0].Profits.length; i++)
            record.push(<td key={key++}>{sum[i]}</td>)
        record.push(<td key={key++}>{totalsum}</td>)
        tbody.push(<tr key={this.state.profit_data.length}>{record}</tr>)

        return tbody
    }

    render() {
        return (
            <div className="profitstat">
                <table>
                    <thead>{this.showThead()}</thead>
                    <tbody>{this.showTbody()}</tbody>
                </table>
            </div>
        )
    }
}

export default Profit_statistics