import React, { Component } from "react";
import Select from '@material-ui/core/Select';
import { FormControl, FormLabel } from "@material-ui/core";
import './index.css'

class DwDrawHistory extends Component {
    constructor(props) {
        super(props)
        this.state = {
            count: 100
        }
        this.drawTbody = this.drawTbody.bind(this)
        this.handleChange = this.handleChange.bind(this)
    }
    drawTbody() {
        if (this.props.drawhistory.length === 0) return null
        var tbody = [], key = 0
        for (let i = 0; i < this.state.count; i++) {
            if (this.props.drawhistory[i].Numbers === "") continue
            var record = []
            record.push(<td key={key++}>{this.props.drawhistory[i].idno}</td>)
            record.push(<td key={key++}>{this.props.drawhistory[i].CTime.slice(5)}</td>)
            var results = JSON.parse(this.props.drawhistory[i].Results)
            record.push(<td key={key++}><div className="draw">
                <div className="num1">{results[0]}</div>
                <div className="num2">{results[1]}</div>
                <div className="num3">{results[2]}</div>
            </div></td>)

            record.push(<td key={key++} className="da">{this.props.drawhistory[i].Draw1_1}</td>)
            record.push(<td key={key++} className={this.props.drawhistory[i].DrawName1_2==="小"?"xiao":"da"}>{this.props.drawhistory[i].DrawName1_2}</td>)
            record.push(<td key={key++} className={this.props.drawhistory[i].DrawName1_3==="双"?"xiao":"da"}>{this.props.drawhistory[i].DrawName1_3}</td>)

            record.push(<td key={key++} className="da">{this.props.drawhistory[i].Draw3_0}</td>)
            record.push(<td key={key++} className={this.props.drawhistory[i].DrawName2_2==="小"?"xiao":"da"}>{this.props.drawhistory[i].DrawName2_2}</td>)
            record.push(<td key={key++} className={this.props.drawhistory[i].DrawName2_3==="双"?"xiao":"da"}>{this.props.drawhistory[i].DrawName2_3}</td>)

            var reclass = "he"
            if (this.props.drawhistory[i].DrawName3==="虎") reclass="xiao"
            else if (this.props.drawhistory[i].DrawName3==="龙") reclass="da"
            record.push(<td key={key++} className={reclass}>{this.props.drawhistory[i].DrawName3}</td>)
            tbody.push(<tr key={i}>{record}</tr>)
        }
        return tbody
    }
    handleChange(e) {
        this.setState({ count: e.target.value })
    }
    render() {
        return (
            <div className="dwdrawhistory">
                <div className="title">
                    <FormLabel>走势图&nbsp;&nbsp;&nbsp;</FormLabel>
                    <FormControl variant="outlined">
                        <Select native onChange={this.handleChange}>
                            <option value={100}>100</option>
                            <option value={200}>200</option>
                            <option value={300}>300</option>
                            <option value={400}>400</option>
                            <option value={500}>500</option>
                        </Select>
                    </FormControl>
                </div>
                <table>
                    <thead>
                        <tr>
                            <th>期号</th>
                            <th>投注时间</th>
                            <th>开奖结果</th>
                            <th colSpan="3">前二</th>
                            <th colSpan="3">后二</th>
                            <th>龙虎和</th>
                        </tr>
                    </thead>
                    <tbody>{this.drawTbody()}</tbody>
                </table>
            </div>
        )
    }
}

export default DwDrawHistory