import React, { Component } from 'react'
import { Link, withRouter } from 'react-router-dom'
import axios from 'axios'
import { Form, Col } from 'react-bootstrap'
import Alert from '@material-ui/lab/Alert'
import urls from '../../urls.json'
import "../../css/Auth/Register.css"
import { Button } from '@material-ui/core'

const AGENT_LIST_URL = urls.BASE_URL + urls.AGENT_LIST
const REGISTER_URL = urls.BASE_URL + urls.REGISTER

class Register extends Component {
    constructor(props) {
        super(props)
        this.state = {
            Username: '',
            Password: '',
            Name: '',
            Agentid_name_list: [],
            AgentId: '',
            selectindex: 0,
            validated: false,
            message: '',
            b_message: false,
            selectinvalid: false
        }
        this.onChangeUsername = this.onChangeUsername.bind(this)
        this.onChangePassword = this.onChangePassword.bind(this)
        this.onChangeName = this.onChangeName.bind(this)
        this.onSubmit = this.onSubmit.bind(this)
    }

    async componentDidMount() {
        localStorage.setItem('nKey', 8)
        await axios.get(AGENT_LIST_URL).then(async (res) => {
            if (res.data.topics.length === 0) return
            res.data.topics.unshift({ id: -1, Name: "" })
            await this.setState({
                Agentid_name_list: res.data.topics,
                AgentId: res.data.topics[0].id
            })
        }).catch(err => {
            console.log(err)
        })
        document.getElementsByTagName("select").selectedIndex = -1
    }

    componentWillUnmount = () => {
        this.setState = (state, callback) => {
            return;
        };
    }

    async onSubmit(e) {
        e.preventDefault()
        const form = e.currentTarget
        // await this.setState({ selectinvalid: (this.state.AgentId === "" ? true : false) })
        if (form.checkValidity() === true && this.state.selectinvalid === false) {
            var data = "Username=" + this.state.Username + "&Password=" + this.state.Password + "&Name=" + this.state.Name + "&AgentId=" + this.state.AgentId
            if (this.state.AgentId === -1) data = "Username=" + this.state.Username + "&Password=" + this.state.Password + "&Name=" + this.state.Name
            await axios.post(REGISTER_URL, data).then(res => {
                if (res.data.type === 'success') {
                    alert(res.data.msg)
                    this.props.history.push('/login')
                } else {
                    this.setState({ message: res.data.msg, b_message: true })
                }
            }).catch(err => {
                console.log(err)
            })
        } else {
            await this.setState({ validated: true })
        }
    }

    onChangeUsername(e) {
        this.setState({ Username: e.target.value, b_message: false })
    }

    onChangePassword(e) {
        this.setState({ Password: e.target.value, b_message: false })
    }

    onChangeName(e) {
        this.setState({ Name: e.target.value, b_message: false })
    }

    async ItemSelectHandler(event) {
        await this.setState({ selectinvalid: false, AgentId: this.state.Agentid_name_list[event.target.value].id, b_message: false, selectindex: event.target.value })
    }

    showSelect() {
        var record = []
        for (let i = 0; i < this.state.Agentid_name_list.length; i++)
            record.push(<option value={i} key={i}>{this.state.Agentid_name_list[i].Name}</option>)
        return <Form.Control as="select" value={this.state.selectindex} placeholder="代理(必填)" onChange={this.ItemSelectHandler.bind(this)} className={"form-control " + (this.state.selectinvalid === true ? "invalidselect" : "")}>
            {record}
        </Form.Control>
    }

    render() {
        return (
            <div className="Register containerwidth">
                <div className="l_Center news">
                    <div className="news-con">
                        <div className="l_Clearfix news_rewrapper">
                            <div className="news-recommend">
                                <div className="u_m-title">
                                    <h2>草莓娱乐账号注册<Link to="/login" className="f_12px">已有帐号？立即登录</Link></h2>
                                </div>
                                <Alert severity="error" className={this.state.b_message === true ? "errorMessage_show" : "errorMessage_hide"}>{this.state.message}</Alert>
                                <div className="news-recommend-list bs-example">
                                    <Form noValidate validated={this.state.validated} onSubmit={this.onSubmit}>
                                        <Form.Group as={Col} controlId="validationCustom01">
                                            <Form.Label>注册账号(<span style={{color: 'red'}}>*</span>至少六位数字或字母组成)</Form.Label>
                                            <Form.Control required type="text" placeholder="注册账号(必填)" onChange={this.onChangeUsername} pattern="[A-Za-z0-9]{6,}" />
                                        </Form.Group>
                                        <Form.Group as={Col} controlId="validationCustom02">
                                            <Form.Label>注册密码</Form.Label>
                                            <Form.Control required type="password" placeholder="密码(必填)" onChange={this.onChangePassword} />
                                        </Form.Group>
                                        <Form.Group as={Col} controlId="validationCustom03">
                                            <Form.Label>用户昵称</Form.Label>
                                            <Form.Control required type="text" placeholder="昵称(必填)" onChange={this.onChangeName} />
                                        </Form.Group>
                                        <Form.Group as={Col} controlId="validationCustom04">
                                            <Form.Label>代理ID</Form.Label>
                                            {this.showSelect()}
                                        </Form.Group>
                                        <Button variant="contained" color="primary" type="submit">点击注册</Button>
                                    </Form>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default withRouter(Register);