import React, { Component } from "react";
import './index.css'
import { TableContainer, Paper, TableHead, Table, TableRow, TableCell, TableBody } from "@material-ui/core";
import Pagination from '@material-ui/lab/Pagination'

class Userprofile extends Component {
    constructor(props) {
        super(props)
        this.showuserinfo = this.showuserinfo.bind(this)
        this.showloginhistory = this.showloginhistory.bind(this)
        this.changePagination = this.changePagination.bind(this)
        this.showcoininfo = this.showcoininfo.bind(this)
        this.state = {
            page_num: 1
        }
    }

    componentDidMount() {
    }

    formatNumber(num) {
        return String(num).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,')
    }

    showuserinfo() {
        if (this.props.userinfo.length === 0) return null
        return <div className="userinfo">
            <div className="rowtitle"><p className="title">用户&nbsp;&nbsp;&nbsp;ID</p><p>:</p><p className="titlecontent">{this.props.userinfo.topics.idno}</p></div>
            <div className="rowtitle"><p className="title">帐　　号</p><p>:</p><p className="titlecontent">{this.props.userinfo.topics.Username}</p></div>
            <div className="rowtitle"><p className="title">邮箱绑定</p><p>:</p><p className="titlecontent">{this.props.userinfo.topics.Email}</p></div>
            <div className="rowtitle"><p className="title">乐　　豆</p><p>:</p><p className="titlecontent">{this.formatNumber(this.props.userinfo.topics.TotalAmount)}</p></div>
            <div className="rowtitle"><p className="title">最后登录时间</p><p>:</p><p className="titlecontent">{this.props.userinfo.topics.LastAccessTime}</p></div>
            <div className="rowtitle"><p className="title">最后登录IP</p><p>:</p><p className="titlecontent">{this.props.userinfo.topics.LastAccessIP}</p></div>
        </div>
    }

    showcoininfo() {
        if (this.props.userinfo.length === 0) return null
        return <div className="coininfo">
            <p>金币余额:&nbsp;&nbsp;&nbsp;&nbsp;<font className="coin">{this.props.userinfo.topics.TotalAmount}</font></p>
            <div className="coincontent">今天充值:&nbsp;&nbsp;<font className="amount">{this.props.userinfo.topics.TodayRechargedAmount}</font>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;今日盈亏:&nbsp;&nbsp;<font className="amount">{this.props.userinfo.topics.TodayWinnedAmount}</font></div>
        </div>
    }

    async changePagination(event, page_num) {
        await this.setState({ page_num: page_num })
    }

    showloginhistory() {
        if (this.props.Login_records.length === 0) return null
        var tbody = [], end = 10 * this.state.page_num > this.props.Login_records.length ? this.props.Login_records.length : 10 * this.state.page_num
        for (let i = 10 * (this.state.page_num - 1); i < end; i++) {
            tbody.push(<TableRow key={i}>
                <TableCell align="center">{this.props.Login_records[i].CTime}</TableCell>
                <TableCell align="center">{this.props.Login_records[i].IP}</TableCell>
                <TableCell align="center">{this.props.Login_records[i].Location}</TableCell>
                <TableCell align="center">{this.props.Login_records[i].Device}</TableCell>
            </TableRow>)
        }
        return <div className="loginhistory">
            <p>最新登陆记录</p>
            <TableContainer component={Paper}>
                <Table stickyHeader>
                    <TableHead>
                        <TableRow>
                            <TableCell align="center">时间</TableCell>
                            <TableCell align="center">IP</TableCell>
                            <TableCell align="center">地点</TableCell>
                            <TableCell align="center">状态</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {tbody}
                    </TableBody>
                </Table>
            </TableContainer>
        </div>
    }

    render() {
        return (
            <div className="userprofile">
                {this.showuserinfo()}
                {/* {this.showcoininfo()} */}
                {this.showloginhistory()}
                <div className="pagination">
                    <Pagination count={this.props.Loginrecordpages} variant="outlined" shape="rounded" onChange={this.changePagination} />
                </div>
            </div>
        )
    }
}

export default Userprofile