import React from 'react';
import { HashRouter as Router, Route, Redirect } from 'react-router-dom'      //npm install react-router-dom
import Home from './views/NewHome'
import Business_Cooperation from './views/Business_Cooperation'
import Activity_Area from './views/Activity_Area'
import Game_centre from './views/Game_centre'
import Member_Centre from './views/Member_Centre'
import News from './views/News'
import TopMenu from './views/TopMenu'
import Login from './views/Authentication/Login'
import Register from './views/Authentication/Register'
import News_detail from './views/News_detail'
import PrivateRoute from './views/Authentication/private-route'
import Recharge from './views/Recharge'
import Withdrawl from './views/Withdrawl'
import RechargeCalc from './views/RechargeCalc'
import WithdrawlCalc from './views/WithdrawlCalc'
import './App.css';
import { useDispatch, useSelector } from 'react-redux'

function tidio() {
  if (window.location.hash) {
    var hash = window.location.hash.substring(1); //Puts hash in variable, and removes the # character
    const s = document.getElementById("tidio-chat-iframe");

    if (hash === '/home');
    if (s) s.style.display = "block !important";
    else
      if (s) s.style.display = "none !important";
    // hash found
  }
}

function App() {
  // const dispatch = useDispatch()
  // const amount = useSelector(state => state.amount)
  // const isLoggedin = useSelector(state => state.isLoggedin)
  const navKey = useSelector(state => state.navKey)

  return (
    <div className="App">
      <Router>
        <TopMenu />
        <Route exact path="/" render={() => (<Redirect to="/home" />)} />
        <Route path="/regame_centre" render={() => (<Redirect to="/game_centre" />)} />
        <Route path='/home' component={() => <Home />} />
        <Route path='/news' component={() => <News />} />
        <PrivateRoute path='/member_centre' component={() => <Member_Centre />} />
        <Route path='/business_cooperation' component={() => <Business_Cooperation />} />
        <Route path='/activity_area' component={() => <Activity_Area />} />
        <Route path='/login' component={() => <Login />} />
        <Route path='/register' component={() => <Register />} />
        <Route path='/news_detail/:id' component={() => <News_detail />} />
        <PrivateRoute path='/game_centre' component={() => <Game_centre  />} />
        <PrivateRoute path='/recharge' component={() => <Recharge />} />
        <PrivateRoute path='/withdrawl' component={() => <Withdrawl />} />
        <PrivateRoute path='/recharge-calc/:id' component={() => <RechargeCalc />} />
        <PrivateRoute path='/withdrawl-calc/:id' component={() => <WithdrawlCalc />} />
      </Router>
    </div>
  );
}

export default App;
