import React, { Component } from 'react'
import "../css/News_detail.css"
import axios from 'axios'
import { withRouter } from 'react-router-dom'
import urls from '../urls.json'
import { Button } from '@material-ui/core'

const NEWS_DETAIL_URL = urls.BASE_URL + urls.NEWS_DETAIL

class News_detail extends Component {
    constructor(props) {
        super(props)
        this.state = {
            News_detail: {},
            latest_news: []
        }
        this.reload = this.reload.bind(this)
    }

    componentDidMount() {
        localStorage.setItem('nKey', 1)
        this.reload(this.props.match.params.id)
    }

    reload(id) {
        axios.get(NEWS_DETAIL_URL + "&id=" + id).then(async (res) => {
            await this.setState({
                News_detail: res.data.topics.Data,
                latest_news: res.data.topics.RelativeNews
            })
        }).catch(err => {
            console.log(err);
        })
    }

    componentWillUnmount = () => {
        this.setState = (state, callback) => {
            return;
        };
    }

    render() {
        return (
            <div className="News_detail containerwidth">
                <div className="l_Center news">
                    {/* <div className="news-nav">
                        <Link to="/home">首页</Link>
                        &gt;
                        <Link to="/news">新闻公告</Link>
                        &gt;
                        <span className="c_name">{this.state.News_detail.Title}</span>
                    </div> */}
                    <div className="news-con">
                        <div id="NIE-art">
                            <h1 className="artTitle">{this.state.News_detail.Title}</h1>
                            <p className="artInfo">
                                <span className="artDate">{this.state.News_detail.CDate}</span>
                            </p>
                            <div className="artText">
                                {this.state.News_detail.Text}
                            </div>
                        </div>
                        <div className="l_Clearfix news_rewrapper">
                            <div className="news-recommend">
                                <div className="u_m-title">
                                    <h3>推荐阅读</h3>
                                </div>
                                <ul className="news-recommend-list">
                                    {this.state.latest_news.map((ne, i) => {
                                        if (this.state.latest_news[i].id !== this.state.News_detail.id)
                                            return <li key={i}>
                                                <Button onClick={() => this.reload(this.state.latest_news[i].id)}>{this.state.latest_news[i].Title}
                                                    <span className="time">{this.state.latest_news[i].CDate}</span>
                                                </Button>
                                            </li>
                                    })}
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default withRouter(News_detail)