                                    {this.props.Game_id.includes("canada") ? <div>采用加拿大快乐8数据，每4分钟一期，每天336期，每天19:00-20:30暂停开奖<br /><p>当加拿大夏令时，开奖时间为北京时间每天20:00至第二天19:00；当加拿大冬令时，开奖时间为北京时间每天21:00至第二天20:00。</p></div> : "采用意大利官方10 e lotto 数据，每5分钟开一次奖，全天24小时开奖。"}
