import React, { Component } from "react";
import Select from '@material-ui/core/Select';
import { FormControl, FormLabel } from "@material-ui/core";
import axios from 'axios'
import Auth from '../../Authentication/auth'
import urls from '../../../urls.json'
import './index.css'

const BET_LIST_URL = urls.BASE_URL + urls.BET_LIST

class DrawHistory extends Component {
    constructor(props) {
        super(props)
        this.state = {
            count: 100,
            drawNameArray: [],
            drawNamecount: 0,
        }
        this.handleChange = this.handleChange.bind(this)
        this.drawThead = this.drawThead.bind(this)
        this.drawTbody = this.drawTbody.bind(this)
        this.testMiddle = this.testMiddle.bind(this)
    }
    componentDidMount() {
        axios.post(BET_LIST_URL + "&gameId=" + this.props.Game_id + "&token=" + Auth.getToken()).then(res => {
            var drawNameArray = []
            if (res.data.type === 'success') {
                drawNameArray.push("期号")
                drawNameArray.push("时间")
                if (this.props.Game_id.includes("lhb")) {
                    this.setState({ drawNamecount: 28 })
                    for (let i = 0; i < 28; i++) drawNameArray.push(i)
                }
                else {
                    this.setState({ drawNamecount: res.data.topics.length })
                    for (let i = 0; i < res.data.topics.length; i++) drawNameArray.push(res.data.topics[i].DrawName)
                }

                drawNameArray.push("单")
                drawNameArray.push("双")
                drawNameArray.push("中")
                drawNameArray.push("边")
                drawNameArray.push("大")
                drawNameArray.push("小")
                drawNameArray.push("大")
                drawNameArray.push("小")
                drawNameArray.push("3/")
                drawNameArray.push("4/")
                drawNameArray.push("5/")
                this.setState({ drawNameArray: drawNameArray })
            }
        }).catch(err => {
            console.log(err)
        })
    }

    componentWillUnmount = () => {
        this.setState = (state, callback) => {
            return;
        };
    }
    handleChange(e) {
        this.setState({ count: e.target.value })
    }
    testMiddle(num) {
        var count = Math.floor(this.state.drawNamecount / 2)
        var start = Math.ceil((this.state.drawNamecount - count) / 2)
        var end = Number(start) + Number(count)
        if (num >= start && num < end) return true
        return false
    }
    drawThead() {
        var thead = []
        for (let i = 0; i < this.state.drawNameArray.length; i++)
            thead.push(<th key={i}>{this.state.drawNameArray[i]}</th>)
        return <tr>{thead}</tr>
    }
    drawTbody() {
        var tbody = [], key = 0
        if (this.props.drawhistory.length === 0) return tbody
        for (let i = 0; i < this.state.count; i++) {
            if(this.props.drawhistory[i]) {
                if (this.props.drawhistory[i].Numbers === "") continue
                var record = [], results = JSON.parse(this.props.drawhistory[i].Results)

                var Draw = 0
                for (let i = 0; i < results.length; i++) Draw += Number(results[i])

                record.push(<td key={key++}>{this.props.drawhistory[i].idno}</td>)
                record.push(<td key={key++}>{this.props.drawhistory[i].CTime.slice(5)}</td>)

                var middle = false, big = false

                if (this.props.Game_id.includes("36")) {
                    var value
                    Draw = this.props.drawhistory[i].DrawName
                    for (let j = 0; j < this.state.drawNamecount; j++) {
                        var Num = ""
                        if (Draw === this.state.drawNameArray[2 + j]) {
                            Num = Draw
                            value = j + 1
                        }
                        if (!this.testMiddle(j)) record.push(<td className={"bgnum " + (Num === "" ? "" : "final")} key={key++}>{Num}</td>)
                        else {
                            if (Num !== "") middle = true
                            record.push(<td className={"nobgnum " + (Num === "" ? "" : "final_red")} key={key++}>{Num}</td>)
                        }
                        if (Num !== "" && j > Math.floor(this.state.drawNamecount / 2)) big = true
                    }
                    Draw = value
                } else {
                    for (let j = 0; j < this.state.drawNamecount; j++) {
                        Num = Number(Draw) === Number(this.state.drawNameArray[2 + j]) ? Draw : ""
                        if (!this.testMiddle(j)) record.push(<td className={"bgnum " + (Num === "" ? "" : "final")} key={key++}>{Num}</td>)
                        else {
                            if (Num !== "") middle = true
                            record.push(<td className={"nobgnum " + (Num === "" ? "" : "final_red")} key={key++}>{Num}</td>)
                        }
                        if (Num !== "" && j >= Math.floor(this.state.drawNamecount / 2)) big = true
                    }
                }
            }else continue


            if (Number(Draw) % 2 !== 0) record.push(<td key={key++} className="bgkai01">单</td>)
            else record.push(<td key={key++}></td>)

            if (Number(Draw) % 2 === 0) record.push(<td key={key++} className="bgkai02">双</td>)
            else record.push(<td key={key++}></td>)

            if (middle) record.push(<td key={key++} className="bgkai03">中</td>)
            else record.push(<td key={key++}></td>)

            if (!middle) record.push(<td key={key++} className="bgkai04">边</td>)
            else record.push(<td key={key++}></td>)

            if (big) record.push(<td key={key++} className="bgkai05">大</td>)
            else record.push(<td key={key++}></td>)

            if (!big) record.push(<td key={key++} className="bgkai06">小</td>)
            else record.push(<td key={key++}></td>)

            if (Number(Draw) % 10 >= 5) record.push(<td key={key++} className="bgkai07">大</td>)
            else record.push(<td key={key++}></td>)

            if (Number(Draw) % 10 < 5) record.push(<td key={key++} className="bgkai08">小</td>)
            else record.push(<td key={key++}></td>)

            record.push(<td key={key++} className="black333">{Number(Draw) % 3}</td>)
            record.push(<td key={key++} className="black333">{Number(Draw) % 4}</td>)
            record.push(<td key={key++} className="black333">{Number(Draw) % 5}</td>)

            tbody.push(<tr key={i}>{record}</tr>)
        }
        return tbody
    }
    render() {
        return (
            <div className="drawhistory">
                <div className="title">
                    <FormLabel>走势图&nbsp;&nbsp;&nbsp;</FormLabel>
                    <FormControl variant="outlined">
                        <Select native onChange={this.handleChange}>
                            <option value={100}>100</option>
                            <option value={200}>200</option>
                            <option value={300}>300</option>
                            <option value={400}>400</option>
                            <option value={500}>500</option>
                        </Select>
                    </FormControl>
                </div>
                <table>
                    <thead>{this.drawThead()}</thead>
                    <tbody>{this.drawTbody()}</tbody>
                </table>
            </div>
        )
    }
}

export default DrawHistory