import React, { Component } from 'react'
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import ProfilePage from "./Dashboard/ProfilePage"
import Safety_management from "./Dashboard/Safety_management"
import Userprofile from "./Dashboard/Userprofile"
import Redemptionrecord from "./Dashboard/Redemptionrecord"
import { withRouter } from "react-router-dom"
import Button from '@material-ui/core/Button'
import "../css/Member_centre.css"
import axios from 'axios'
import Auth from './Authentication/auth'
import urls from '../urls.json'

const PROFILE_URL = urls.BASE_URL + urls.PROFILE
const Latest_login_records_URL = urls.BASE_URL + urls.LOGIN_RECORDS
const Redemption_history_URL = urls.BASE_URL + urls.REDEMPTION

function TabPanel(props) {
    const { children, value, index, ...other } = props;
    return (
        <Typography component="div" role="tabpanel" hidden={value !== index} id={`vertical-tabpanel-${index}`} aria-labelledby={`vertical-tab-${index}`} {...other}>
            {value === index && <Box p={3}>{children}</Box>}
        </Typography>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired,
};

function a11yProps(index) {
    return {
        id: `vertical-tab-${index}`,
        'aria-controls': `vertical-tabpanel-${index}`,
    };
}
const useStyles = makeStyles(theme => ({
    root: {
        flexGrow: 1,
        backgroundColor: theme.palette.background.paper,
        display: 'flex',
        // height: 224,
    },
    tabs: {
        borderRight: `1px solid ${theme.palette.divider}`,
    },
}));

class Member_Centre extends Component {
    constructor(props) {
        super(props);
        this.state = {
            value: 0,
            userinfo: [],
            Login_records: [],
            Loginrecordpages: 1,
            Redemptionhistory: [],
            redemptionpages: 1
        };
        this.handleChange = this.handleChange.bind(this)
        this.reload = this.reload.bind(this)
    }

    async componentDidMount() {
        localStorage.setItem('nKey', 5)
        await this.reload()
    }

    async reload() {
        await axios.post(PROFILE_URL + "&token=" + Auth.getToken()).then(async (res) => {
            if (res.data.type === "success") {
                await this.setState({ userinfo: res.data })
                axios.get(Latest_login_records_URL + "&token=" + Auth.getToken()).then(async (res) => {
                    if (res.data.topics.length !== 0) {
                        await this.setState({
                            Login_records: res.data.topics,
                            Loginrecordpages: Math.ceil(Number(res.data.topics.length) / 10)
                        })
                    }
                }).catch(err => {
                    console.log(err)
                })
            } else {
                if (localStorage.getItem("token") !== null) {
                    await Auth.deauthenticateUser()
                    alert("您的登录状态无效，请重新登录。")
                    window.location.reload()
                }
            }
        }).catch(err => {
            console.log(err)
        })

        await axios.get(Redemption_history_URL + "&token=" + Auth.getToken()).then(async (res) => {
            if (res.data.type === "success") {
                await this.setState({ Redemptionhistory: res.data.topics, redemptionpages: Math.ceil(Number(res.data.topics.length) / 10) })
            }
        })
    }

    componentWillUnmount = () => {
        this.setState = (state, callback) => {
            return;
        };
    }

    handleChange(event, newValue) {
        this.setState({ value: newValue })
    }

    Goto_Game_centre() {
        this.props.history.push('/game_centre')
    }

    render() {
        return (
            <div className="containerwidth dashboard">
                {
                    Number(localStorage.getItem('userType')) === 0 ?
                        <div className="guestUserdisplay">游客没有权限，请注册会员</div> :
                        <div className="membercentre">
                            <div className="Tab_area">
                                <Tabs orientation="vertical" value={this.state.value} onChange={this.handleChange} aria-label="Vertical tabs example" className={useStyles.tabs} >
                                    <Tab label={<span className="Tab_label Tab_Header">个人中心首页</span>} {...a11yProps(0)} />
                                    <Tab label={<div className="Tab_area_subtitle" ><h2>会员信息</h2></div>} {...a11yProps(1)} disabled />
                                    <Tab label={<span className="Tab_label">我的资料</span>} {...a11yProps(2)} />
                                    <Tab label={<span className="Tab_label">兑换记录</span>} {...a11yProps(3)} />
                                    <Tab label={<div className="Tab_area_subtitle" ><h2>安全管理</h2></div>} {...a11yProps(4)} disabled />
                                    <Tab label={<span className="Tab_label">密码修改</span>} {...a11yProps(5)} />
                                </Tabs>
                                <div className="button_area">
                                    <Button variant="contained" className="button_area_action" color="secondary" onClick={(e) => this.Goto_Game_centre(e)}>进入游戏</Button>
                                </div>
                            </div>
                            <div className="Tab_panel">
                                <TabPanel value={this.state.value} index={0}><Userprofile userinfo={this.state.userinfo} Login_records={this.state.Login_records} Loginrecordpages={this.state.Loginrecordpages} /></TabPanel>
                                <TabPanel value={this.state.value} index={2}><ProfilePage reload={this.reload} userinfo={this.state.userinfo} /></TabPanel>
                                <TabPanel value={this.state.value} index={3}><Redemptionrecord Redemptionhistory={this.state.Redemptionhistory} redemptionpages={this.state.redemptionpages} /></TabPanel>
                                <TabPanel value={this.state.value} index={5}><Safety_management /></TabPanel>
                            </div>
                        </div>
                }

            </div>
        )
    }
}

export default withRouter(Member_Centre)
