import React from 'react'
import { Navbar, Nav, NavDropdown } from 'react-bootstrap'
import "../css/TopMenu.css"
import logosecond from "../images/home/logo.png"
import Auth from './Authentication/auth'
import { withRouter } from "react-router-dom"
import { Link } from "react-router-dom"
import member_a from "../images/navbar/member_a.svg"
import member_u from "../images/navbar/member_u.svg"
import register_a from "../images/navbar/register_a.svg"
import register_u from "../images/navbar/register_u.svg"
import login_a from "../images/navbar/login_a.svg"
import login_u from "../images/navbar/login_u.svg"
import logout from "../images/navbar/logout.svg"
import momenttimezone from "moment-timezone"

class TopMenu extends React.Component {
  constructor(props) {
    super(props);
    this.LogOut = this.LogOut.bind(this)
    this.LogIn = this.LogIn.bind(this)
    this.handleStart = this.handleStart.bind(this)
    this.setTime = this.setTime.bind(this)
    this.drawLog = this.drawLog.bind(this)
    this.state = {
      myInterval: -1,
      showDateTime: '',
      isLoggedIn: false
    }
  }

  async componentDidMount() {
    this.handleStart()
  }

  componentWillUnmount = () => {
    this.setState = (state, callback) => {
      return;
    };
  }

  async LogOut() {
    await Auth.deauthenticateUser()
    window.location.reload()
    // alert("登出了.")
  }
  LogIn() {
    this.props.history.push('/login')
    this.setState({ isLoggedIn: true })
  }

  Goto_cooperation() {
    this.props.history.push('/business_cooperation')
  }

  handleStart() {
    this.setState({
      myInterval: setInterval(this.setTime, 500)
    })
  }

  async setTime() {
    await this.setState({
      showDateTime: "北京时间 " + momenttimezone().tz("Asia/Shanghai").year() + "/" +
        ((momenttimezone().tz("Asia/Shanghai").month() + 1) < 10 ? "0" + (momenttimezone().tz("Asia/Shanghai").month() + 1) : (momenttimezone().tz("Asia/Shanghai").month() + 1)) + "/" +
        (momenttimezone().tz("Asia/Shanghai").date() < 10 ? "0" + momenttimezone().tz("Asia/Shanghai").date() : momenttimezone().tz("Asia/Shanghai").date()) + " "
        + (momenttimezone().tz("Asia/Shanghai").hour() < 10 ? "0" + momenttimezone().tz("Asia/Shanghai").hour() : momenttimezone().tz("Asia/Shanghai").hour()) + ":"
        + (momenttimezone().tz("Asia/Shanghai").minute() < 10 ? "0" + momenttimezone().tz("Asia/Shanghai").minute() : momenttimezone().tz("Asia/Shanghai").minute()) + ":"
        + (momenttimezone().tz("Asia/Shanghai").second() < 10 ? "0" + momenttimezone().tz("Asia/Shanghai").second() : momenttimezone().tz("Asia/Shanghai").second())
    })
  }

  formatNumber(num) {
    return String(num).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,')
  }

  drawLog() {
    if (Auth.isUserAuthenticated()) {
      return <div className={"topnav"}>
        <Link to="/home" onClick={this.LogOut}><img src={logout} /><p>登出</p></Link>
      </div>
    }
    var record = []
    record.push(
      <div className={"topnav " + (localStorage.getItem('nKey') === "7" ? "tn_active" : "")} key={1}>
        <Link to="/login">{localStorage.getItem('nKey') === "7" ? <img src={login_a} /> : <img src={login_u} />}<p>会员登录</p></Link>
      </div>
    )
    record.push(
      <div className={"topnav " + (localStorage.getItem('nKey') === "8" ? "tn_active" : "")} key={2}>
        <Link to="/register">{localStorage.getItem('nKey') === "8" ? <img src={register_a} /> : <img src={register_u} />}<p>免费注册</p></Link>
      </div>
    )
    return record
  }

  render() {
    return (
      <div>
        <div className="background_black">
          <div className="top_bar">
            <h5 className="time_shower">{this.state.showDateTime}</h5>
            {Auth.isUserAuthenticated() ?
              <div className="person_information">
                <h5 className="validated_name">您好, {localStorage.getItem("validated_Name")}</h5>
                <h5 className="validated_amount">乐豆: <strong>{this.formatNumber(localStorage.getItem("TotalAmount"))}</strong></h5>
              </div> : null
            }
          </div>
        </div>
        <div className="header">
          <div className="logo"> <Link to="/home"> <img src={logosecond} width="160px" /> </Link>
          </div>
          <div className="Sign_in_out">
            {this.drawLog()}
            <div className={"topnav " + (localStorage.getItem('nKey') === "6" ? "tn_active" : "")}>
              <Link to="/business_cooperation">
                {localStorage.getItem('nKey') === "6" ? <img src={member_a} /> : <img src={member_u} />}
                <p>商务合作</p>
              </Link>
            </div>
          </div>
        </div>
        <Navbar collapseOnSelect expand="lg" variant="dark">
          <Navbar.Toggle aria-controls="responsive-navbar-nav" />
          <Navbar.Collapse id="responsive-navbar-nav">
            <Nav className="mr-auto containerwidth">
              <Nav.Link href="#home" className={localStorage.getItem('nKey') === "0" ? "active" : "unactive"} >首页</Nav.Link>
              <Nav.Link href="#news" className={localStorage.getItem('nKey') === "1" ? "active" : "unactive"}>新闻资讯</Nav.Link>
              <Nav.Link href="#activity_area" className={localStorage.getItem('nKey') === "2" ? "active" : "unactive"}>活动专区</Nav.Link>
              <Nav.Link href="#game_centre" className={localStorage.getItem('nKey') === "3" ? "active" : "unactive"}>游戏中心</Nav.Link>
              {
                Auth.isUserAuthenticated() ? <NavDropdown title="商城" id="collasible-nav-dropdown" className={localStorage.getItem('nKey') === "4" ? "active" : "unactive"}>
                  <NavDropdown.Item href="#recharge">充值</NavDropdown.Item>
                  <NavDropdown.Item href="#withdrawl">提款</NavDropdown.Item>
                </NavDropdown>
                  : <Nav.Link href="#recharge" className={localStorage.getItem('nKey') === "4" ? "active" : "unactive"}>商城</Nav.Link>
              }
              <Nav.Link href="#member_centre" className={localStorage.getItem('nKey') === "5" ? "active" : "unactive"}>会员中心</Nav.Link>
            </Nav>
          </Navbar.Collapse>
        </Navbar>
      </div>
    )
  }
}
export default withRouter(TopMenu);