import React, { Component } from 'react'
import "../css/recharge.css"
import { Link } from "react-router-dom"
import axios from 'axios'
import Auth from './Authentication/auth'
import urls from '../urls.json'
import { withRouter } from "react-router-dom"


const PROFILE_URL = urls.BASE_URL + urls.PROFILE

class Recharge extends Component {
    componentDidMount() {
        localStorage.setItem('nKey', 4)
        this.authTest()
    }
    componentWillUnmount = () => {
        this.setState = (state, callback) => {
            return;
        };
    }
    async authTest() {
        await axios.post(PROFILE_URL + "&token=" + Auth.getToken()).then(async (res) => {
            if (res.data.type === "needlogin" || res.data.type === "fail") {
                if (localStorage.getItem("token") !== null) {
                    alert("您的登录状态无效，请重新登录。")
                    await Auth.deauthenticateUser()
                    window.location.reload()
                }
            } else {
                localStorage.setItem('nKey', 4)
            }
        }).catch(err => {
            console.log(err);
        })
    }

    render() {
        return (
            <div className="containerwidth">
                {
                    Number(localStorage.getItem('userType')) === 0 ?
                        <div className="guestUserdisplay" style={{ marginTop: '10px' }}>游客没有权限，请注册会员</div> :
                        <div className="recharge">
                            <div className="recharge-row">
                                <div className="price">
                                    <Link to="/business_cooperation" className="p_header">50,000,000豆</Link>
                                    <p className="p_title">50000￥</p>
                                    <Link to="/business_cooperation">立即兑换</Link>
                                </div>
                                <div className="price">
                                    <Link to="/business_cooperation" className="p_header">10,000,000豆</Link>
                                    <p className="p_title">10000￥</p>
                                    <Link to="/business_cooperation">立即兑换</Link>
                                </div>
                                <div className="price">
                                    <Link to="/business_cooperation" className="p_header">5,000,000豆</Link>
                                    <p className="p_title">5000￥</p>
                                    <Link to="/business_cooperation">立即兑换</Link>
                                </div>
                            </div>
                            <div className="recharge-row">
                                <div className="price">
                                    <Link to="/business_cooperation" className="p_header">1,000,000豆</Link>
                                    <p className="p_title">1000￥</p>
                                    <Link to="/business_cooperation">立即兑换</Link>
                                </div>
                                <div className="price">
                                    <Link to="/business_cooperation" className="p_header">500,000豆</Link>
                                    <p className="p_title">500￥</p>
                                    <Link to="/business_cooperation">立即兑换</Link>
                                </div>
                                <div className="price">
                                    <Link to="/business_cooperation" className="p_header">100,000豆</Link>
                                    <p className="p_title">100￥</p>
                                    <Link to="/business_cooperation">立即兑换</Link>
                                </div>
                            </div>
                        </div>
                }
            </div>
        )
    }
}

export default withRouter(Recharge)