import React, { Component } from 'react'
import "../../css/dashboard/Safety_management.css"
import VpnKeyIcon from '@material-ui/icons/VpnKey'
import Button from '@material-ui/core/Button'
import { Form, Col } from 'react-bootstrap'
import Alert from '@material-ui/lab/Alert'
import Auth from '../Authentication/auth'
import axios from 'axios'
import urls from '../../urls.json'

const CHANGE_PASSWORD_URL = urls.BASE_URL + urls.CHANGE_PASSWORD
const CHANGE_SECURITYPASSWORD_URL = urls.BASE_URL + urls.CHANGE_SECURITYPASSWORD

class Safety_management extends Component {
    constructor(props) {
        super(props)
        this.state = {
            Old_password: '',
            New_password: '',
            confirm_New_password: '',
            validated: false,
            message: '',
            b_message: false,
            alert_type: 'error',
            b_confirmpwd: true,

            Old_security_password: '',
            New_security_password: '',
            confirm_Newsecurity_password: '',
            validated_1: false,
            message_1: '',
            b_message_1: false,
            alert_type_1: 'error',
            b_confirmpwd_1: true,
        };
        this.onChange_Old_password = this.onChange_Old_password.bind(this);
        this.onChange_New_password = this.onChange_New_password.bind(this);
        this.onChange_confirm_New_password = this.onChange_confirm_New_password.bind(this);
        this.onSubmit = this.onSubmit.bind(this);

        this.onChange_Old_security_password = this.onChange_Old_security_password.bind(this);
        this.onChange_New_security_password = this.onChange_New_security_password.bind(this);
        this.onChange_confirm_New_security_password = this.onChange_confirm_New_security_password.bind(this);
        this.onSubmit_security_password = this.onSubmit_security_password.bind(this);
    }

    onChange_Old_password(e) {
        this.setState({
            Old_password: e.target.value,
            b_message: false
        })
    }

    async onChange_New_password(e) {
        await this.setState({ New_password: e.target.value, b_message: false })
        if (this.state.validated === true) {
            await this.setState({ b_confirmpwd: this.state.New_password === this.state.confirm_New_password })
        }
    }

    async onChange_confirm_New_password(e) {
        await this.setState({ confirm_New_password: e.target.value, b_message: false })
        if (this.state.validated === true) {
            await this.setState({ b_confirmpwd: this.state.New_password === this.state.confirm_New_password })
        }
    }

    onChange_Old_security_password(e) {
        this.setState({
            Old_security_password: e.target.value,
            b_message_1: false
        })
    }

    async onChange_New_security_password(e) {
        await this.setState({ New_security_password: e.target.value, b_message_1: false })
        if (this.state.validated_1 === true) {
            await this.setState({ b_confirmpwd_1: this.state.New_security_password === this.state.confirm_Newsecurity_password })
        }
    }

    async onChange_confirm_New_security_password(e) {
        await this.setState({ confirm_Newsecurity_password: e.target.value, b_message_1: false })
        if (this.state.validated_1 === true) {
            await this.setState({ b_confirmpwd_1: this.state.New_security_password === this.state.confirm_Newsecurity_password })
        }
    }

    async onSubmit(e) {
        e.preventDefault()
        const form = e.currentTarget
        await this.setState({ validated: true, b_confirmpwd: this.state.New_password === this.state.confirm_New_password })
        if (form.checkValidity() === true && this.state.b_confirmpwd === true) {
            e.preventDefault();
            this.setState({
                validated: true
            })
            if (this.state.confirm_New_password !== this.state.New_password) {
                this.setState({
                    alert_type: 'error', b_message: true, message: "confirm your new password", New_password: '', confirm_New_password: ''
                })
            }
            const data = "Old=" + this.state.Old_password + "&New=" + this.state.New_password
            await axios.post(CHANGE_PASSWORD_URL + "&token=" + Auth.getToken(), data).then(res => {
                Auth.authenticateUser(res.data.token)
                if (res.data.msg === "操作成功") {
                    this.setState({
                        alert_type: 'success', b_message: true, message: res.data.msg,
                        Old_password: '', New_password: '', confirm_New_password: '', validated: false
                    })
                } else {
                    if (res.data.msg === '旧的密码错误') {
                        this.setState({ alert_type: 'error', b_message: true, message: res.data.msg, Old_password: '', New_password: '', confirm_New_password: '', validated: false })
                    } else if (res.data.type === 'needlogin') {
                        this.setState({ alert_type: 'error', b_message: true, message: "Your Token information is incorrect.Login again!" })
                    }
                }
            }).catch(err => {
                console.log("Error in change password!");
            })
        }
    }

    async onSubmit_security_password(e) {
        e.preventDefault()
        const form = e.currentTarget
        await this.setState({ validated_1: true, b_confirmpwd_1: this.state.New_security_password === this.state.confirm_Newsecurity_password })
        if (form.checkValidity() === true && this.state.b_confirmpwd_1 === true) {
            e.preventDefault();
            this.setState({
                validated_1: true
            })
            if (this.state.confirm_Newsecurity_password !== this.state.New_security_password) {
                this.setState({
                    alert_type_1: 'error', b_message_1: true, message_1: "confirm your new password", New_security_password: '', confirm_Newsecurity_password: ''
                })
            }
            const data = "Old=" + this.state.Old_security_password + "&New=" + this.state.New_security_password
            await axios.post(CHANGE_SECURITYPASSWORD_URL + "&token=" + Auth.getToken(), data).then(res => {
                Auth.authenticateUser(res.data.token)
                if (res.data.msg === "操作成功") {
                    this.setState({
                        alert_type_1: 'success', b_message_1: true, message_1: res.data.msg,
                        Old_security_password: '', New_security_password: '', confirm_Newsecurity_password: '', validated_1: false
                    })
                } else {
                    if (res.data.msg === '旧的安全密码错误') {
                        this.setState({ alert_type_1: 'error', b_message_1: true, message_1: res.data.msg, Old_security_password: '', New_security_password: '', confirm_Newsecurity_password: '', validated_1: false })
                    } else if (res.data.type === 'needlogin') {
                        this.setState({ alert_type_1: 'error', b_message_1: true, message_1: "Your Token information is incorrect.Login again!" })
                    }
                }
            }).catch(err => {
                console.log("Error in change security password!");
            })
        }
    }

    render() {
        return (
            <div className="Safety_management">
                <div className="password_modify">
                    <div className="safety_title">
                        <VpnKeyIcon />
                        <h2>登录密码</h2>
                    </div>
                    <div className="safety_input">
                        <Alert severity={this.state.alert_type} className={this.state.b_message === true ? "Alert_show" : "Alert_hide"}>{this.state.message}</Alert>
                        <Form className="form-safety_input" noValidate validated={this.state.validated} onSubmit={this.onSubmit}>
                            <Form.Group as={Col} controlId="validationCustom01" className="safety_input_field">
                                <p>原登录密码</p>
                                <Form.Control className="input_field" type="password" value={this.state.Old_password} onChange={this.onChange_Old_password} required />
                            </Form.Group>
                            <br />
                            <Form.Group as={Col} controlId="validationCustom02" className="safety_input_field">
                                <p>新登录密码</p>
                                <Form.Control className="input_field" type="password" value={this.state.New_password} onChange={this.onChange_New_password} required />
                            </Form.Group>
                            <br />
                            <Form.Group as={Col} controlId="validationCustom03" className="safety_input_field">
                                <p>确认新密码</p>
                                <Form.Control className={(this.state.b_confirmpwd === true ? "" : "invalidconfirmpwd") + " input_field"} type="password" value={this.state.confirm_New_password} onChange={this.onChange_confirm_New_password} required />
                            </Form.Group>
                            <br />
                            <div className="button_field">
                                <Button type="submit" variant="contained" color="secondary">
                                    马上修改
                            </Button>
                            </div>

                        </Form>
                    </div>

                    <div className="password_modify">
                        <div className="safety_title">
                            <VpnKeyIcon />
                            <h2>安全密码</h2>
                        </div>
                        <div className="safety_input">
                            <Alert severity={this.state.alert_type_1} className={this.state.b_message_1 ? "Alert_show" : "Alert_hide"}>{this.state.message_1}</Alert>
                            <Form className="form-safety_input" noValidate validated={this.state.validated_1} onSubmit={this.onSubmit_security_password}>
                                <Form.Group as={Col} controlId="validationCustom04" className="safety_input_field">
                                    <p>原安全密码</p>
                                    <Form.Control className="input_field" type="password" value={this.state.Old_security_password} onChange={this.onChange_Old_security_password} required />
                                    <p>原始密码与登陆密码相同</p>
                                </Form.Group>
                                <br />
                                <Form.Group as={Col} controlId="validationCustom05" className="safety_input_field">
                                    <p>新安全密码</p>
                                    <Form.Control className="input_field" type="password" value={this.state.New_security_password} onChange={this.onChange_New_security_password} required />
                                </Form.Group>
                                <br />
                                <Form.Group as={Col} controlId="validationCustom06" className="safety_input_field">
                                    <p>确认新密码</p>
                                    <Form.Control className={(this.state.b_confirmpwd_1 ? "" : "invalidconfirmpwd") + " input_field"} type="password" value={this.state.confirm_Newsecurity_password} onChange={this.onChange_confirm_New_security_password} required />
                                </Form.Group>
                                <br />
                                <div className="button_field">
                                    <Button type="submit" variant="contained" color="secondary">
                                        马上修改
                                    </Button>
                                </div>
                            </Form>
                        </div>
                    </div>

                </div>
            </div>
        )
    }
}
export default Safety_management;
