import React, { Component } from "react";
import { Link, withRouter } from 'react-router-dom'
import Helmet from "react-helmet"
import "./index.css"
import axios from "axios"
import Auth from '../Authentication/auth'
import crausolimg from "../../images/home/crausol1.png"
import canada_28 from "../../images/home/canada_28.png"
import italy_28 from "../../images/home/italy_28.png"
import beijing_28 from "../../images/home/beijing_28.png"
import beijing_dw from "../../images/home/beijing_dw.png"
import canada_dw from "../../images/home/canada_dw.png"
import italy_lhb from "../../images/home/italy_lhb.png"
import indexserver from "../../images/home/index_server.jpg"
import qqimg from "../../images/home/qq.png"
import urls from '../../urls.json'
import { TextField, Button, Snackbar } from "@material-ui/core"
import MuiAlert from '@material-ui/lab/Alert'
import goOnline from '../../images/home/21_online.gif'

const POPOVER_URL = urls.BASE_URL + urls.POPOVER
const HOME_URL = urls.BASE_URL + urls.HOME
const LOGIN_URL = urls.BASE_URL + urls.LOGIN
const PROFILE_URL = urls.BASE_URL + urls.PROFILE
const GET_QQ_URL = urls.BASE_URL + urls.GET_QQ_URL
const GUESTLOGIN_URL = urls.BASE_URL + urls.GUESTLOGIN

function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

class NewHome extends Component {
    constructor(props) {
        super(props)
        this.state = {
            news: [],
            Username: "",
            Password: "",
            validated_id_no: localStorage.getItem('validated_id_no'),
            validated_Name: localStorage.getItem('validated_Name'),
            totalAmount: localStorage.getItem('TotalAmount'),
            successopen: false,
            erroropen: false,
            message: "",
            loginorloggedin: Auth.isUserAuthenticated(),
            popup_open: false,
            Modal_title: "",
            Modal_text: "",
            fromtop: 250,
            qqlist: []
        }
        this.onChangeUsername = this.onChangeUsername.bind(this)
        this.onChangePassword = this.onChangePassword.bind(this)
        this.onSubmit = this.onSubmit.bind(this)
        this.handleClose = this.handleClose.bind(this)
        this.onRegister = this.onRegister.bind(this)
        this.goCanada28 = this.goCanada28.bind(this)
        this.goItaly28 = this.goItaly28.bind(this)
        this.goNews = this.goNews.bind(this)
        this.goBeijing28 = this.goBeijing28.bind(this)
        this.goBeijingdw = this.goBeijingdw.bind(this)
        this.goCanadadw = this.goCanadadw.bind(this)
        this.goItalylhb = this.goItalylhb.bind(this)
        this.getPopup = this.getPopup.bind(this)
        this.authTest = this.authTest.bind(this)
        this.goMemberCentre = this.goMemberCentre.bind(this)
        this.handleScroll = this.handleScroll.bind(this)
        this.goOnline = this.goOnline.bind(this)
        this.getQQList = this.getQQList.bind(this)
        this.forgotPWD = this.forgotPWD.bind(this)
        this.onGuest = this.onGuest.bind(this)
    }

    componentDidMount() {
        window.addEventListener('scroll', this.handleScroll)
        localStorage.setItem('nKey', 0)
        this.authTest()
        this.getPopup()
        this.getNews()
        this.getQQList()
    }

    componentWillUnmount = () => {
        this.setState = (state, callback) => {
            return;
        };
    }

    async forgotPWD() {
        await this.setState({ successopen: true })
    }

    async handleScroll(event) {
        await this.setState({ fromtop: 250 + window.scrollY })
    }

    async authTest() {
        await axios.post(PROFILE_URL + "&token=" + Auth.getToken()).then(async (res) => {
            if ((res.data.type === "needlogin" || res.data.type === "fail") && Auth.isUserAuthenticated()) {
                alert("您的登录状态无效，请重新登录。")
                Auth.deauthenticateUser()
                window.location.reload()
            }
        }).catch(err => {
            console.log(err)
        })
    }

    async getQQList() {
        await axios.get(GET_QQ_URL).then(async (res) => {
            if (res.data.type === "success") {
                await this.setState({ qqlist: res.data.topics })
            } else {
                if (localStorage.getItem("token") !== null) {
                    alert("您的登录状态无效，请重新登录。")
                    Auth.deauthenticateUser()
                    window.location.reload()
                }
            }
        })
    }

    async getPopup() {
        await axios.get(POPOVER_URL).then(res => {
            this.setState({
                Modal_title: res.data.topics.Title,
                Modal_text: res.data.topics.Text,
                popup_open: true
            })
        }).catch(err => {
            console.log('Error from banner_request');
        })
    }

    async onChangeUsername(e) {
        await this.setState({ Username: e.target.value })
    }

    async onChangePassword(e) {
        await this.setState({ Password: e.target.value })
    }

    formatNumber(num) {
        return String(num).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,')
    }

    onSubmit() {
        const data = "Username=" + this.state.Username + "&" + "Password=" + this.state.Password;
        axios.post(LOGIN_URL, data).then(async (res) => {
            if (res.data.type === 'success') {
                Auth.authenticateUser(res.data.token)
                await this.setState({
                    validated_id_no: res.data.topics.idno,
                    totalAmount: res.data.topics.TotalAmount,
                    validated_Name: res.data.topics.Name,
                    loginorloggedin: Auth.isUserAuthenticated()
                })
                Auth.setUserinfo(res.data.topics.idno, res.data.topics.Name, res.data.topics.TotalAmount, res.data.topics.TypeId)
            }
            else this.setState({ message: "登录失败,请重试!", erroropen: true })
        }).catch(err => {
            console.log(err)
        })
    }

    onGuest() {
        axios.get(GUESTLOGIN_URL).then(async (res) => {
            if (res.data.type === 'success') {
                Auth.authenticateUser(res.data.token)
                await this.setState({
                    validated_id_no: res.data.topics.idno,
                    validated_Name: res.data.topics.Name,
                    totalAmount: res.data.topics.TotalAmount,
                    loginorloggedin: Auth.isUserAuthenticated()
                })
                Auth.setUserinfo(res.data.topics.idno, res.data.topics.Name, res.data.topics.TotalAmount, res.data.topics.TypeId)
            } else this.setState({ message: "登录失败,请重试!", erroropen: true })
        }).catch(err => {
            console.log(err)
        })
    }

    handleClose() {
        this.setState({
            successopen: false,
            erroropen: false,
            popup_open: false,
        })
    }

    async getNews() {
        await axios.get(HOME_URL).then(res => {
            this.setState({
                news: res.data.topics.News,
            })
        }).catch(err => {
            console.log(err);
        })
    }

    showNews() {
        if (this.state.news.length === 0) return null
        var record = [], key = 0
        for (let i = 0; i < this.state.news.length; i++) {
            record.push(<Button key={key++} onClick={() => this.goNews(i + 1)}>[新闻]{this.state.news[i].Title}</Button>)
        }
        return <marquee loop="-1">{record}</marquee>
    }

    goNews(news_id) {
        this.props.history.push("/news_detail/" + news_id)
    }

    showLogin() {
        if (this.state.loginorloggedin) {
            return <div className="login">
                <h3 className="loginheader">欢迎登陆现代推广员</h3>
                <div className="log_reg"><h4>用户ID</h4><h5>{this.state.validated_id_no}</h5></div>
                <div className="log_reg"><h4>用户名</h4><h5>{this.state.validated_Name}</h5></div>
                <div className="log_reg"><h4>乐豆</h4><h5>{this.formatNumber(this.state.totalAmount)}</h5></div>
                <Button variant="contained" color="secondary" className="loginbutton" onClick={this.goMemberCentre}>进入个人中心</Button>
            </div>
        }
        return <div className="login">
            <h3 className="loginheader">会员登陆<br />MEMBER LOGIN </h3>
            <div className="loginform">
                <TextField name="username" placeholder="请输入注册邮箱" onChange={this.onChangeUsername} variant="outlined" />
                <TextField name="password" placeholder="请输入密码" type="password" onChange={this.onChangePassword} variant="outlined" className="inputpwd" />
                <a className="forgotpwd" onClick={this.forgotPWD}>忘记密码</a>
                <Button variant="contained" color="primary" className="loginbutton" onClick={this.onSubmit}>登 录</Button>
                <Button variant="contained" color="primary" className="loginbutton guest" onClick={this.onGuest}>游客登陆</Button>
                <Button variant="contained" color="secondary" className="loginbutton" onClick={this.onRegister}>免费开户</Button>
            </div>
        </div>
    }

    onRegister() {
        this.props.history.push('/register')
    }

    goCanada28() {
        localStorage.setItem('parentKey', "Canada")
        localStorage.setItem('canadaKey', "canada_28")
        this.props.history.push("/game_centre")
    }

    goItaly28() {
        localStorage.setItem('parentKey', "Italy")
        localStorage.setItem('italyKey', "italy_28")
        this.props.history.push("/game_centre")
    }

    goBeijing28() {
        this.props.history.push("/game_centre")
    }

    goBeijingdw() {
        this.props.history.push("/game_centre")
    }

    goCanadadw() {
        localStorage.setItem("parentKey", "Canada")
        localStorage.setItem("canadaKey", "canada_dw")
        this.props.history.push("/game_centre")
    }

    goItalylhb() {
        localStorage.setItem("parentKey", "Italy")
        localStorage.setItem("italyKey", "italy_lhb")
        this.props.history.push("/game_centre")
    }

    goMemberCentre() {
        this.props.history.push("/member_centre")
    }

    showPopup() {
        if (!this.state.popup_open) return
        var popup = []
        popup.push(<div key={0} className="xc_layer"></div>)
        popup.push(<div key={1} className="popBox">
            <div className="ttBox">
                <span className="tt">{this.state.Modal_title}</span>
            </div>
            <div className="txtBox"><p>{this.state.Modal_text}</p></div>
            <div className="btnArea">
                <Button className="sgBtn" variant="contained" onClick={this.handleClose}>确定</Button>
            </div>
        </div>)
        return popup
    }

    goOnline() {
        window.open("https://reek.masarn.com/chat/chatClient/chatbox.jsp?companyID=365026637&jid=8835520513", "Chatbox", "width=1000, height=800")
    }

    showQQList() {
        if (this.state.qqlist.length === 0) return null
        var record = []
        for (let i = 0; i < this.state.qqlist.length; i++) {
            record.push(<div className="tel-num" key={i}><a>{this.state.qqlist[i]}</a></div>)
        }
        return record
    }

    render() {
        return (
            <div className="newhome" id="home-id">
                <Helmet style={[{
                    "cssText": `
                        #tidio-chat-iframe {
                        top: 300px !important;
                        display: block !important;
                    }`}]} />
                {this.showPopup()}
                {/* <div className="floater" style={{ top: this.state.fromtop }}>
                    <img src={goOnline} onClick={this.goOnline} />
                </div> */}
                <Snackbar open={this.state.erroropen} autoHideDuration={2000} onClose={this.handleClose} anchorOrigin={{ vertical: "top", horizontal: "center" }}>
                    <Alert severity="error"> {this.state.message} </Alert>
                </Snackbar>

                <div className="main-im">
                    <div className="im_main">
                        {/* <div className="close_im">
                            <a className="closelink" href="#" title="点击关闭">&nbsp;</a>
                        </div> */}
                        <a className="qq-a" title="在线QQ客服" target="_blank">
                            <div className="qq-container"><div className="qq-container2"></div></div>
                            <div className="qq-hover-c"><img className="img-qq" src={qqimg} /></div>
                            <span> QQ在线咨询</span>
                        </a>
                        <div className="im-tel">
                            {this.showQQList()}
                            {/* <div className="tel-num"><a target="_blank" href="http://wpa.qq.com/msgrd?v=3&amp;uin=877000028&amp;site=qq&amp;menu=yes">877000028</a></div>
                            <div className="tel-num"><a target="_blank" href="http://wpa.qq.com/msgrd?v=3&amp;uin=577000028&amp;site=qq&amp;menu=yes">577000028</a></div>
                            <div className="tel-num"><a target="_blank" href="http://wpa.qq.com/msgrd?v=3&amp;uin=791192277&amp;site=qq&amp;menu=yes">791192277</a></div>
                            <div>官方交流群</div> */}
                            <div className="tel-num"><a target="_blank" href=""></a></div>

                        </div>

                    </div>
                </div>

                <Link to="/activity_area"><img className="crausolimg" src={crausolimg} /></Link>
                <div className="homenews">
                    <div className="containerwidth">
                        <div className="zxnews">
                            <Link to="/news"></Link>
                        </div>
                        {this.showNews()}
                    </div>
                </div>
                <div className="main">
                    <div className="containerwidth">
                        <div className="mainheader">
                            <p>热门游戏</p>
                        </div>
                        <div className="content">
                            <div className="left_main">
                                <div className="gameType">
                                    <img className="gameimg" src={canada_28} onClick={this.goCanada28} />
                                    <img className="gameimg gamexyun" src={italy_28} onClick={this.goItaly28} />
                                </div>
                                <div className="gameType secondtype">
                                    <img className="beijing28" src={beijing_28} onClick={this.goBeijing28} />
                                    <img className="gamerecommend" src={beijing_dw} onClick={this.goBeijingdw} />
                                    <img className="gamerecommend" src={canada_dw} onClick={this.goCanadadw} />
                                    <img className="gamerecommend" src={italy_lhb} onClick={this.goItalylhb} />
                                </div>
                            </div>
                            <div className="right_main">
                                {this.showLogin()}
                            </div>
                        </div>
                    </div>
                </div>
                <div className="w1080"><img src={indexserver} /></div>
            </div>
        )
    }
}

export default withRouter(NewHome)