import React, { Component } from "react";
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Pagination from '@material-ui/lab/Pagination';
import './index.css'
import { Dialog, DialogTitle, IconButton, Button } from "@material-ui/core";
import axios from 'axios'
import Auth from '../../Authentication/auth'
import CloseIcon from '@material-ui/icons/Close'
import coinimg from '../../../images/Coin.png'
import urls from '../../../urls.json'

const BET_LIST_URL = urls.BASE_URL + urls.BET_LIST

class BetHistory extends Component {
    constructor(props) {
        super(props)
        this.state = {
            records: 10,
            pages: 1,
            page_num: 1,
            betopen: false,
            showbetDialogdata: [],
            showbetDialogdata_row: [],
        }
        this.makeTbody = this.makeTbody.bind(this)
        this.changePagination = this.changePagination.bind(this)
        this.betDialog = this.betDialog.bind(this)
        this.betDclose = this.betDclose.bind(this)
        this.betTbody = this.betTbody.bind(this)
    }
    
    componentDidMount() {
    }

    async updatePagination() {
        await this.setState({ pages: Math.ceil(this.props.bethistory.length / this.state.records) })
    }

    makeTbody() {
        if (this.props.bethistory.length === 0) return null
        var tbody = []
        var start = (this.state.page_num - 1) * this.state.records
        var end = this.state.page_num * this.state.records
        if (end > this.props.bethistory.length) end = this.props.bethistory.length
        for (let i = start; i < end; i++) {
            var draw
            if (!this.props.Game_id.includes("dw")) draw = <TableCell align="center"><div className="bet"><div className="betresult">{this.props.bethistory[i].DrawNames}</div></div></TableCell>
            else draw = <TableCell align="center"><div className="bet"><div className="betresult">{this.props.bethistory[i].DrawNames[this.props.bethistory[i].DrawNames.length - 1]}</div></div></TableCell>
            tbody.push(<TableRow key={i}>
                <TableCell component="th" scope="row" align="center">{this.props.bethistory[i].SpecGameIdno}</TableCell>
                <TableCell align="center">{this.props.bethistory[i].CTime}</TableCell>
                {draw}
                <TableCell align="center">{this.props.bethistory[i].BetAmount}</TableCell>
                <TableCell align="center">{this.props.bethistory[i].DrawAmount}</TableCell>
                <TableCell align="center">{Number(this.props.bethistory[i].DrawAmount) - Number(this.props.bethistory[i].BetAmount)}</TableCell>
                <TableCell align="center"><Button variant="contained" className="linkbtn" onClick={() => this.betDialog(i)}>查看</Button></TableCell>
            </TableRow>)
        }
        return tbody
    }

    changePagination(event, page_num) {
        this.setState({ page_num: page_num })
    }

    async betDialog(index) {
        await axios.post(BET_LIST_URL + "&gameId=" + this.props.Game_id + "&drawId=" + this.props.bethistory[index].SpecGameIdno + "&token=" + Auth.getToken()).then(async (res) => {
            if (res.data.type === 'success') {
                await this.setState({showbetDialogdata: res.data.topics, showbetDialogdata_row: this.props.bethistory[index], betopen: true})
            }
        }).catch(err => {
            console.log(err)
        })
    }

    betDclose() {
        this.setState({ betopen: false })
    }

    betTbody() {
        if (this.state.showbetDialogdata.length === 0) return null
        var tbody = [], key = 0
        for (let i = 0; i < this.state.showbetDialogdata.length; i++) {
            var record = []
            record.push(<td key={key++}><div className="bet"><div className="betNum">{this.state.showbetDialogdata[i].DrawName}</div></div></td>)
            record.push(<td key={key++}>{this.state.showbetDialogdata[i].Percent}</td>)
            record.push(<td key={key++}>{this.state.showbetDialogdata[i].BetAmount}</td>)
            record.push(<td key={key++}>{this.state.showbetDialogdata[i].DrawAmount}</td>)
            tbody.push(<tr key={i}>{record}</tr>)
        }
        return tbody
    }
    render() {
        return (
            <div className="bethistory">
                <TableContainer component={Paper}>
                    <Table aria-label="simple table">
                        <TableHead>
                            <TableRow>
                                <TableCell align="center">期号</TableCell>
                                <TableCell align="center">投注时间</TableCell>
                                <TableCell align="center">开奖结果</TableCell>
                                <TableCell align="center">投注数量</TableCell>
                                <TableCell align="center">获得数量</TableCell>
                                <TableCell align="center">赢取</TableCell>
                                <TableCell align="center">详情</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {this.makeTbody()}
                        </TableBody>
                    </Table>
                </TableContainer>
                <div className="pagination">
                    <Pagination count={this.state.pages} variant="outlined" shape="rounded" onChange={this.changePagination} />
                </div>
                <Dialog open={this.state.betopen} aria-labelledby="simple-dialog-title" className="betDialog">
                    <DialogTitle id="dialog-title">
                        <p>第{this.state.showbetDialogdata_row.idno}期投注详细结果</p>
                        <IconButton aria-label="close" onClick={this.betDclose}>
                            <CloseIcon />
                        </IconButton>
                    </DialogTitle>
                    <div className="secondtitle">开奖时间:{this.state.showbetDialogdata_row.CTime} 投注:{this.state.showbetDialogdata_row.BetAmount}<img src={coinimg} alt="coin" /> 获得:{this.state.showbetDialogdata_row.DrawAmount}<img src={coinimg} alt="coin" /></div>
                    <div className="dialogcontent">
                        <table>
                            <thead>
                                <tr>
                                    <th>号码</th>
                                    <th>开奖赔率</th>
                                    <th>投注数量</th>
                                    <th>获得数量</th>
                                </tr>
                            </thead>
                            <tbody>
                                {this.betTbody()}
                            </tbody>
                        </table>
                    </div>
                </Dialog>
            </div>

        )
    }
}

export default BetHistory