import React, { Component } from "react";
import "./index.css"
import { TableContainer, Table, Paper, TableHead, TableRow, TableCell, TableBody } from "@material-ui/core";
import Pagination from '@material-ui/lab/Pagination'

class Redemptionrecord extends Component {
    constructor(props) {
        super(props)
        this.state = {
            page_num: 1
        }
        this.showTbody = this.showTbody.bind(this)
        this.changePagination = this.changePagination.bind(this)
    }

    componentDidMount() {
    }

    formatNumber(num) {
        return String(num).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,')
    }

    showTbody() {
        if (this.props.Redemptionhistory.length === 0) return
        var record = [], end = this.state.page_num * 10 > this.props.Redemptionhistory.length ? this.props.Redemptionhistory.length : this.state.page_num * 10
        for (let i = (this.state.page_num - 1) * 10; i < end; i++) {
            record.push(<TableRow key={i}>
                <TableCell align="center">{this.props.Redemptionhistory[i].CTime}</TableCell>
                <TableCell align="center">{this.props.Redemptionhistory[i].FromUserName}</TableCell>
                <TableCell align="center">{this.formatNumber(this.props.Redemptionhistory[i].Amount)}</TableCell>
                <TableCell align="center">{this.formatNumber(this.props.Redemptionhistory[i].Currency)}</TableCell>
                <TableCell align="center">{this.props.Redemptionhistory[i].Key}</TableCell>
                <TableCell align="center" className="statewidth">{this.props.Redemptionhistory[i].StateName}</TableCell>
            </TableRow>)
        }
        return record
    }

    async changePagination(event, page_num) {
        await this.setState({ page_num: page_num})
    }

    render() {
        return (
            <div className="redemptionrecord">
                <div className="redemptionhistory">
                    <p>兑换记录</p>
                    <TableContainer component={Paper}>
                        <Table stickyHeader>
                            <TableHead>
                                <TableRow>
                                    <TableCell align="center">生成时间</TableCell>
                                    <TableCell align="center">代理或管理员名称</TableCell>
                                    <TableCell align="center">乐豆</TableCell>
                                    <TableCell align="center">提款金额</TableCell>
                                    <TableCell align="center">密卡号</TableCell>
                                    <TableCell align="center">状态</TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {this.showTbody()}
                            </TableBody>
                        </Table>
                    </TableContainer>
                </div>
                <div className="pagination">
                    <Pagination count={this.props.redemptionpages} variant="outlined" shape="rounded" onChange={this.changePagination} />
                </div>
            </div>
        )
    }
}

export default Redemptionrecord