import React, { Component } from 'react'
import axios from 'axios'
import Auth from './Authentication/auth'

import "../css/Business_Cooperation.css"
import rw from "../images/rw.jpg"
import urls from '../urls.json'

const PROFILE_URL = urls.BASE_URL + urls.PROFILE
const AGENT_LIST_URL = urls.BASE_URL + urls.AGENT_LIST

class Business_Cooperation extends Component {
    constructor(props) {
        super(props)
        this.state = {
            Agentid_name_list: []
        }
        this.showList = this.showList.bind(this)
    }

    componentDidMount() {
        localStorage.setItem('nKey', 6)
        axios.post(PROFILE_URL + "&token=" + Auth.getToken()).then(async (res) => {
            if (res.data.type === "needlogin" || res.data.type === "fail") {
                if (localStorage.getItem("token") !== null) {
                    alert("您的登录状态无效，请重新登录。")
                    Auth.deauthenticateUser()
                    window.location.reload()
                }
            }
        }).catch(err => {
            console.log(err)
        })

        axios.get(AGENT_LIST_URL).then((res) => {
            this.setState({ Agentid_name_list: res.data.topics })
        }).catch(err => {
            console.log('Error from AgentId list.');
        })
    }
    componentWillUnmount = () => {
        this.setState = (state, callback) => {
            return;
        };
    }

    showList() {
        if (this.state.Agentid_name_list.length === 0) return null
        var showlist = [], key = 0
        for (let i = 0; i < this.state.Agentid_name_list.length; i++) {
            showlist.push(
                <li className="privilege-item" key={key++}>
                    {/* <a href="http://wpa.qq.com/msgrd?v=3&amp;uin=9474345&amp;site=qq&amp;menu=yes" className="privilege-page-a"> */}
                    <a className="privilege-page-a">
                        <span className="privilege-page-a-card">{this.state.Agentid_name_list[i].Contact}</span>
                        <span className="privilege-page-a-qq">{this.state.Agentid_name_list[i].Name}</span>
                        {/* <div className="privilege-item-mask">123</div> */}
                        <p className="privilege-item-des">{this.state.Agentid_name_list[i].Name}</p>
                    </a>
                </li>
            )
        }
        return showlist
    }
    render() {
        return (
            <div className="containerwidth Business_cooperation">
                <div className="l_Center privilege-page">
                    <div className="privilege-page-banner"><img src={rw} alt="" /></div>
                    <div className="merchans_list">
                        <ul className="privilege-page-list">
                            {this.showList()}
                        </ul>
                    </div>
                </div>
            </div>
        )
    }
}

export default Business_Cooperation;