import React, { Component } from 'react'
import "../../css/dashboard/profile_page.css"
import TextField from '@material-ui/core/TextField';
import Auth from '../Authentication/auth'
import axios from 'axios';
import urls from '../../urls.json'
import { Button } from '@material-ui/core';

const EMAIL_VALIDATE_URL = urls.BASE_URL + urls.EMAIL_VALIDATE
const UPDATE_PROFILE_URL = urls.BASE_URL + urls.UPDATE_PROFILE

class Member_Centre extends Component {
    constructor(props) {
        super(props)
        this.state = {
            Name: "",
            QQ: "",
            Wechat: "",
            Email: ""
        }
        this.showProfilePage = this.showProfilePage.bind(this)
        this.onChangeName = this.onChangeName.bind(this)
        this.onChangeQQ = this.onChangeQQ.bind(this)
        this.onChangeWechat = this.onChangeWechat.bind(this)
        this.onChangeEmail = this.onChangeEmail.bind(this)
        this.validateEmail = this.validateEmail.bind(this)
        this.updateProfile = this.updateProfile.bind(this)
    }

    componentDidMount() {
        this.setState({
            Name: this.props.userinfo.topics.Name,
            QQ: this.props.userinfo.topics.QQ,
            Wechat: this.props.userinfo.topics.Wechat,
            Email: this.props.userinfo.topics.Email
        })
    }

    componentWillUnmount = () => {
        this.setState = (state, callback) => {
            return
        }
    }

    formatNumber(num) {
        return String(num).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,')
    }

    async onChangeName(e) {
        await this.setState({ Name: e.target.value })
    }

    async onChangeQQ(e) {
        await this.setState({ QQ: e.target.value })
    }

    async onChangeWechat(e) {
        await this.setState({ Wechat: e.target.value })
    }

    async onChangeEmail(e) {
        await this.setState({ Email: e.target.value })
    }

    validateEmail() {
        axios.post(EMAIL_VALIDATE_URL + "&token=" + Auth.getToken(), "Email=" + this.state.Email).then(res => {
            if (res.data.type === "success") {
                alert(res.data.msg)
                this.props.reload()
            }
        })
    }

    updateProfile() {
        var data = "Name=" + this.state.Name
        data += "&QQ=" + this.state.QQ + "&Wechat=" + this.state.Wechat + "&Email=" + this.state.Email
        axios.post(UPDATE_PROFILE_URL + "&token=" + Auth.getToken(), data).then(res => {
            alert(res.data.msg)
            if (typeof this.props.reload === 'function') {
                this.props.reload()
            }
        })
    }

    showProfilePage() {
        if (this.props.userinfo.length === 0) return null
        return <div className="profile_page">
            <p className="sub_title">个人资料:</p>
            <br />
            <div className="input_form">
                <div className="input_form_text"><p className="sign">昵 称</p>:</div>
                {
                    this.props.userinfo.topics.Name !== "" ? <TextField className="QQ_text3" variant="filled" value={this.state.Name} disabled inputProps={{ style: { fontSize: "17px" } }} /> :
                        <TextField variant="outlined" className="QQ_text3" inputProps={{ style: { fontSize: "17px" } }} value={this.state.Name} onChange={this.onChangeName} />
                }
                <div className="input_content">{this.props.userinfo.topics.Name === "" ? "填写后不能再修改" : "已设置"}</div>
            </div>
            <div className="input_form">
                <div className="input_form_text"><p className="sign">Q Q</p>:</div>
                {
                    this.props.userinfo.topics.QQ !== "" ? <TextField className="QQ_text3" variant="filled" value={this.state.QQ} disabled inputProps={{ style: { fontSize: "17px" } }} /> :
                        <TextField variant="outlined" className="QQ_text3" inputProps={{ style: { fontSize: "17px" } }} value={this.state.QQ} onChange={this.onChangeQQ} />
                }
                <div className="input_content">{this.props.userinfo.topics.QQ === "" ? "填写后不能再修改" : "已绑定"}</div>
            </div>
            <div className="input_form">
                <div className="input_form_text"><p className="sign">微 信</p>:</div>
                {
                    this.props.userinfo.topics.Wechat !== "" ? <TextField className="QQ_text3" variant="filled" value={this.state.Wechat} disabled inputProps={{ style: { fontSize: "17px" } }} /> :
                        <TextField variant="outlined" className="QQ_text3" inputProps={{ style: { fontSize: "17px" } }} value={this.state.Wechat} onChange={this.onChangeWechat} InputProps={{ disabled: (this.props.userinfo.topics.Wechat !== "") }} />
                }
                <div className="input_content">{this.props.userinfo.topics.Wechat === "" ? "填写后不能再修改" : "已绑定"}</div>
            </div>
            <div className="input_form">
                <div className="input_form_text"><p className="sign">邮 箱</p>:</div>
                {
                    this.props.userinfo.topics.Email !== "" ? <TextField className="QQ_text3" variant="filled" value={this.state.Email} disabled inputProps={{ style: { fontSize: "17px" } }} /> :
                        <TextField variant="outlined" className="QQ_text3" inputProps={{ style: { fontSize: "17px" } }} value={this.state.Email} onChange={this.onChangeEmail} InputProps={{ disabled: (this.props.userinfo.topics.Email !== "") }} />
                }
                <div className="input_content">{this.props.userinfo.topics.QQ === "" ? "填写后不能再修改" : "已绑定"}</div>
                <Button color="secondary" className="emailcheckBtn" variant="contained" hidden={this.props.userinfo.topics.Email !== ""} onClick={this.validateEmail}>验证邮箱</Button>
            </div>
            <Button className="updateBtn" variant="contained" color="primary" onClick={this.updateProfile}>保存信息</Button>
            {/* <div className="input_form">
                <div className="input_form_text"><p className="sign">*</p><p>代理编号:</p></div>
                <TextField className="QQ_text3" inputProps={{ style: { fontSize: "17px" } }} />
            </div>

            <div className="input_form">
                <div className="input_form_text"><p className="sign">*</p><p>Q　　Q:</p></div>
                <TextField className="QQ_text2" inputProps={{ style: { fontSize: "17px" } }} />
                <div className="input_form_text"><p>必须与收款的支付宝姓名一致，填写后不能再修改 </p></div>
            </div>
            <div className="input_form">
                <div className="input_form_text"><p className="sign">*</p><p>会员编号:</p></div>
                <TextField className="QQ_text3" inputProps={{ style: { fontSize: "17px" } }} />
                <div className="input_form_text"><p>填写后不能再修改</p></div>

            </div>
            <div className="input_form">
                <div className="input_form_text"><p className="sign">*</p><p>代理名称:</p></div>
                <TextField className="QQ_text4" id="input-with-icon-grid-1" inputProps={{ style: { fontSize: "17px" } }} />
            </div>
            <Divider className="middle_divider" variant="middle" /> */}
            {/* <div className="sub_title">
            <VerifiedUserIcon /><h2>安全信息</h2>
        </div> */}

            {/* <br /> */}
            {/* <div className="input_form">
            <div className="input_form_text"><p className="sign">*</p><p>绑定邮箱:</p></div>
            <TextField required id="input-with-icon-grid" inputProps={{ style: { fontSize: "17px" } }} />
            <div className="input_form_text"><p>已绑定</p></div>

        </div>
        <div className="input_form">
            <div className="input_form_text"><p className="sign">* </p><p>绑定手机:</p></div>
            <TextField required id="input-with-icon-grid" inputProps={{ style: { fontSize: "17px" } }} />
            <div className="input_form_text"><p>已绑定</p></div>
        </div>
        <div className="button_area">
            <Button
                variant="contained"
                color="secondary"
            // className={classes.but
            >
                保存信息
            </Button>
        </div> */}
        </div>
    }

    render() {
        return (
            <div>
                {this.showProfilePage()}
            </div>
        )
    }
}

export default Member_Centre;