import React, { Component } from 'react'
import { Button, ButtonGroup } from '@material-ui/core'
import '../css/rechargecalc.css'
import '../css/recharge.css'
import axios from 'axios'
import { withRouter } from "react-router-dom"
import urls from '../urls.json'


const RECHARGE_URL = urls.BASE_URL + urls.RECHARGE


var a_price = [50000, 10000, 5000, 1000, 500, 100]
var str_price = ["50,000,000", "10,000,000", "5,000,000", "1,000,000", "500,000", "100,000"]
class RechargeCalc extends Component {
    constructor(props) {
        super(props)
        this.state = {
            totalNumber: 1,
            price: a_price[props.match.params.id - 1],
            totalPrice: a_price[props.match.params.id - 1],
            showCoin: str_price[props.match.params.id - 1],
        }
        this.onAdd = this.onAdd.bind(this)
        this.onMin = this.onMin.bind(this)
        this.onSubmit = this.onSubmit.bind(this)
        this.onChange = this.onChange.bind(this)
    }

    componentDidMount() {
        localStorage.setItem('nKey', 4)
    }

    componentWillUnmount = () => {
        this.setState = (state,callback)=>{
          return;
        };
    }
    
    onChange(index) {
        this.setState({
            price: a_price[index - 1],
            totalPrice: a_price[index - 1],
            showCoin: str_price[index - 1],
            totalNumber: 1
        })
    }

    async onAdd() {
        await this.setState({
            totalNumber: this.state.totalNumber + 1,
            totalPrice: this.state.price * (this.state.totalNumber + 1)
        })
    }

    async onMin() {
        if (this.state.totalNumber >= 2)
            await this.setState({
                totalNumber: this.state.totalNumber - 1,
                totalPrice: this.state.price * (this.state.totalNumber - 1)
            })
    }

    onSubmit() {
        var token = localStorage.getItem('token')
        var url = RECHARGE_URL + "&token=" + token
        axios.post(url, "amount=" + this.state.totalPrice).then(res => {
            if (res.data.type === "success") {
                alert(res.data.msg + "   Key:" + res.data.topics.key)
            } else {
                alert(res.data.msg)
            }
        }).catch(err => {
            console.log(err)
        })
    }

    render() {
        return (
            <div className="containerwidth">
                <div className="rechargecalc">
                    <div className="main">
                        <div className="money">{this.state.showCoin}豆</div>
                        <div className="content">
                            <p className="title">{this.state.showCoin.replace(/,/g,"") * this.state.totalNumber}豆</p>
                            <div className="goods_para">
                                {/* <dl>
                                    <b><dt>兑换限制：</dt></b>
                                    <dd><p>流水没达到充值1倍:不能提现</p></dd>
                                </dl>
                                <dl>
                                    <dt>&nbsp;</dt>
                                    <dd><p>流水不足3倍:收2%手续费</p></dd>
                                </dl>
                                <dl>
                                    <dt>&nbsp;</dt>
                                    <dd><p>流水大于3倍少于4倍:收1%手续费</p></dd>
                                </dl>
                                <dl>
                                    <dt>&nbsp;</dt>
                                    <dd><p>流水大于4倍:免手续费</p></dd>
                                </dl> */}
                                <dl>
                                    <dt>您的购买价：</dt>
                                    <dd><strong className="goods_pice">{this.state.totalPrice}</strong>￥</dd>
                                </dl>
                            </div>
                            <div className="goods_buy">
                                <div className="goods_buy_t">购买数量：</div>
                                <ButtonGroup variant="contained" color="primary" aria-label="contained primary button group">
                                    <Button onClick={this.onMin}>-</Button>
                                    <Button>{this.state.totalNumber}</Button>
                                    <Button onClick={this.onAdd}>+</Button>
                                </ButtonGroup>
                            </div>
                            <Button className="buyButton" variant="contained" onClick={this.onSubmit}>立即兑换</Button>
                        </div>
                    </div>
                </div>
                <div className="containerwidth recharge">
                    <p className="shopheader">您可能喜欢</p>
                    <div className="recharge-row">
                        <div className="price">
                            <a onClick={() => this.onChange(1)} className="p_header">50,000,000豆</a>
                            <p className="p_title">50000￥</p>
                            <a onClick={() => this.onChange(1)}>立即兑换</a>
                        </div>
                        <div className="price">
                            <a onClick={() => this.onChange(2)} className="p_header">10,000,000豆</a>
                            <p className="p_title">10000￥</p>
                            <a onClick={() => this.onChange(2)}>立即兑换</a>
                        </div>
                        <div className="price">
                            <a onClick={() => this.onChange(3)} className="p_header">5,000,000豆</a>
                            <p className="p_title">5000￥</p>
                            <a onClick={() => this.onChange(3)}>立即兑换</a>
                        </div>
                    </div>
                    <div className="recharge-row">
                        <div className="price">
                            <a onClick={() => this.onChange(4)} className="p_header">1,000,000豆</a>
                            <p className="p_title">1000￥</p>
                            <a onClick={() => this.onChange(4)}>立即兑换</a>
                        </div>
                        <div className="price">
                            <a onClick={() => this.onChange(5)} className="p_header">500,000豆</a>
                            <p className="p_title">500￥</p>
                            <a onClick={() => this.onChange(5)}>立即兑换</a>
                        </div>
                        <div className="price">
                            <a onClick={() => this.onChange(6)} className="p_header">100,000豆</a>
                            <p className="p_title">100￥</p>
                            <a onClick={() => this.onChange(6)}>立即兑换</a>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default withRouter(RechargeCalc)