import React, { Component } from 'react'
import axios from 'axios'
import Auth from './auth'
import { withRouter, Link } from "react-router-dom"
import { Form, Col } from 'react-bootstrap'
import Alert from '@material-ui/lab/Alert'
import urls from '../../urls.json'
import "../../css/Auth/Login.css"
import { Button, Snackbar } from '@material-ui/core'
import MuiAlert from '@material-ui/lab/Alert'

const LOGIN_URL = urls.BASE_URL + urls.LOGIN
const GUESTLOGIN_URL = urls.BASE_URL + urls.GUESTLOGIN

function MyAlert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            Username: '',
            Password: '',
            Verification_code: '',
            alert_open: false,
            validated: false,
            message: '',
            b_message: false,
            open: false,
        }
        this.onChangeUsername = this.onChangeUsername.bind(this)
        this.onChangePassword = this.onChangePassword.bind(this)
        this.onChangeVerification_code = this.onChangeVerification_code.bind(this)
        this.handleSubmit = this.handleSubmit.bind(this)
        this.handleClose = this.handleClose.bind(this)
        this.guestLogin = this.guestLogin.bind(this)
    }

    componentDidMount() {
        localStorage.setItem('nKey', 7)
    }

    componentWillUnmount = () => {
        this.setState = (state,callback)=>{
          return;
        };
    }

    onChangeUsername(e) {
        this.setState({ Username: e.target.value, b_message: false })
    }

    onChangePassword(e) {
        this.setState({ Password: e.target.value, b_message: false })
    }

    onChangeVerification_code(e) {
        this.setState({ Verification_code: e.target.value, b_message: false })
    }
    
    guestLogin() {
        axios.get(GUESTLOGIN_URL).then(async(res) => {
            if (res.data.type === 'success') {
                Auth.authenticateUser(res.data.token)
                localStorage.setItem('validated_id_no', res.data.topics.idno)
                localStorage.setItem('validated_Name', res.data.topics.Name)
                localStorage.setItem('TotalAmount', res.data.topics.TotalAmount)
                localStorage.setItem('userType', res.data.topics.TypeId)
                localStorage.setItem('parentKey', "Canada")
                localStorage.setItem('canadaKey', "canada_11")
                localStorage.setItem('italyKey', "italy_11")
                this.props.history.push(this.props.location.state.from.pathname)
            } else {
                this.setState({
                    message: res.data.msg,
                    b_message: true,
                })
            }
        }).catch(err => {
            this.props.history.push('/home')
            console.log(err)
        })
    }

    async handleSubmit(event) {
        event.preventDefault()
        const form = event.currentTarget
        if (form.checkValidity() === true) {
            event.preventDefault()
            const data = "Username=" + this.state.Username + "&Password=" + this.state.Password
            await axios.post(LOGIN_URL, data).then(async(res) => {
                if (res.data.type === 'success') {
                    Auth.authenticateUser(res.data.token)
                    localStorage.setItem('validated_id_no', res.data.topics.idno)
                    localStorage.setItem('validated_Name', res.data.topics.Name)
                    localStorage.setItem('TotalAmount', res.data.topics.TotalAmount)
                    localStorage.setItem('parentKey', "Canada")
                    localStorage.setItem('canadaKey', "canada_11")
                    localStorage.setItem('italyKey', "italy_11")
                    localStorage.setItem('userType', res.data.topics.TypeId)
                    this.props.history.push(this.props.location.state.from.pathname)
                } else {
                    this.setState({
                        message: res.data.msg,
                        b_message: true,
                    })
                }
            }).catch(err => {
                this.props.history.push('/home')
                console.log(err)
            })
        }
        await this.setState({ validated: true })
    }

    async handleClose() {
        await this.setState({ open: false })
    }

    render() {
        return (
            <div className="Login containerwidth">
                <Snackbar open={this.state.open} onClose={this.handleClose} anchorOrigin={{ vertical: "top", horizontal: "center" }}>
                    <MyAlert onClose={this.handleClose} severity="warning">请联系线上服务</MyAlert>
                </Snackbar>
                <div className="news-con">
                    <div className="l_Clearfix news_rewrapper">
                        <div className="news-recommend">
                            <div className="u_m-title">
                                <h2>登录现代娱乐<Link to="/register">没有帐号？立即注册</Link></h2>
                            </div>
                            <Alert severity="error" className={this.state.b_message === true ? "errorMessage_show" : "errorMessage_hide"}>{this.state.message}</Alert>
                            <div className="news-recommend-list bs-example">
                                <Form noValidate validated={this.state.validated} onSubmit={this.handleSubmit} method="post">
                                    <Form.Group as={Col} controlId="validationCustom01">
                                        <Form.Label>帐号</Form.Label>
                                        <Form.Control required placeholder="帐号" onChange={this.onChangeUsername} />
                                        {/* type="email" */}
                                    </Form.Group>
                                    <Form.Group className="passwordgroup" as={Col} controlId="validationCustom02">
                                        <Form.Label>密码</Form.Label>
                                        <Form.Control required type="password" placeholder="密码(必填)" onChange={this.onChangePassword} />
                                    </Form.Group>
                                    <Form.Group as={Col}>
                                        <Button className="forgotpassword" onClick={() => this.setState({open: true})}>忘记密码</Button>
                                    </Form.Group>
                                    <Button variant="contained" color="primary" type="submit" className="loginbtn">点击登录</Button>
                                    <Button variant="contained" color="primary" className="guestbtn" onClick={this.guestLogin}>游客登陆</Button>
                                </Form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default withRouter(Login)