import React, { Component } from 'react'
import { Button, ButtonGroup, Snackbar, TextField } from '@material-ui/core'
import '../css/rechargecalc.css'
import '../css/recharge.css'
import axios from 'axios'
import { withRouter } from 'react-router-dom'
import Auth from './Authentication/auth'
import MuiAlert from '@material-ui/lab/Alert';

import urls from '../urls.json'

const PROFILE_URL = urls.BASE_URL + urls.PROFILE
const WITHDRAWAL_URL = urls.BASE_URL + urls.WITHDRAWAL
var str_price = ["50000", "10000", "5000", "1000", "500", "100"]
var a_price = [50000000, 10000000, 5000000, 1000000, 500000, 100000]

function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

class WithdrawlCalc extends Component {
    constructor(props) {
        super(props)

        this.state = {
            totalNumber: 1,
            price: a_price[props.match.params.id - 1],
            totalPrice: a_price[props.match.params.id - 1],
            showCoin: str_price[props.match.params.id - 1],
            password: "",
            open: false,
            message: "",
            erroropen: false,
        }
        this.onAdd = this.onAdd.bind(this)
        this.onMin = this.onMin.bind(this)
        this.onSubmit = this.onSubmit.bind(this)
        this.onChange = this.onChange.bind(this)
        this.passwordChange = this.passwordChange.bind(this)
        this.handleClose = this.handleClose.bind(this)
    }

    componentDidMount() {
        localStorage.setItem('nKey', 4)
        axios.post(PROFILE_URL + "&token=" + Auth.getToken()).then(async (res) => {
            if (res.data.type === "needlogin" || res.data.type === "fail") {
                if (localStorage.getItem("token") !== null) {
                    alert("您的登录状态无效，请重新登录。")
                    Auth.deauthenticateUser()
                    window.location.reload()
                }
            } else {
                localStorage.setItem("TotalAmount", res.data.topics.TotalAmount)
            }
        }).catch(err => {
            console.log(err)
        })
    }

    componentWillUnmount = () => {
        this.setState = (state, callback) => {
            return;
        };
    }

    onChange(index) {
        this.setState({
            price: a_price[index - 1],
            totalPrice: a_price[index - 1],
            showCoin: str_price[index - 1],
            totalNumber: 1
        })
    }

    async onAdd() {
        await this.setState({
            totalNumber: this.state.totalNumber + 1,
            totalPrice: this.state.price * (this.state.totalNumber + 1)
        })
    }

    async onMin() {
        if (this.state.totalNumber >= 2)
            await this.setState({
                totalNumber: this.state.totalNumber - 1,
                totalPrice: this.state.price * (this.state.totalNumber - 1)
            })
    }

    async onSubmit() {
        var token = localStorage.getItem('token')
        var url = PROFILE_URL + "&token=" + token, totalAmount = 0
        try {
            var user = await axios.post(url)
            totalAmount = user.data.topics.TotalAmount
        } catch (err) {
            console.log(err)
        }

        if (totalAmount >= this.state.totalPrice) {
            url = WITHDRAWAL_URL + "&token=" + token
            axios.post(url, "amount=" + this.state.totalPrice + "&SecurityCode=" + this.state.password).then(async (res) => {
                if (res.data.type === "success") {
                    await axios.post(PROFILE_URL + "&token=" + Auth.getToken()).then(async (res) => {
                        if (res.data.type === "needlogin" || res.data.type === "fail") {
                            if (localStorage.getItem("token") !== null) {
                                alert("您的登录状态无效，请重新登录。")
                                Auth.deauthenticateUser()
                                window.location.reload()
                            }
                        } else {
                            localStorage.setItem("TotalAmount", res.data.topics.TotalAmount)
                        }
                    }).catch(err => {
                        // console.log(err)
                    })
                    this.setState({
                        message: res.data.msg + "   " + res.data.topics.Key,
                        open: true
                    })
                } else {
                    this.setState({
                        message: res.data.msg,
                        erroropen: true
                    })
                }
            }).catch(err => {
                console.log(err)
            })
        } else {
            this.setState({
                message: "账户余额不足",
                erroropen: true
            })
        }
    }

    async passwordChange(e) {
        await this.setState({ password: e.target.value })
    }

    handleClose() {
        this.setState({ open: false, erroropen: false })
    }

    formatNumber(num) {
        return String(num).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,')
    }

    render() {
        return (
            <div className="containerwidth withdrawlcalc">
                <Snackbar open={this.state.open} onClose={this.handleClose} anchorOrigin={{ vertical: "top", horizontal: "center" }}>
                    <Alert onClose={this.handleClose} severity="success">
                        {this.state.message}<br />请复制代码发送给代理
                    </Alert>
                </Snackbar>
                <Snackbar open={this.state.erroropen} onClose={this.handleClose} anchorOrigin={{ vertical: "top", horizontal: "center" }}>
                    <Alert onClose={this.handleClose} severity="error">
                        {this.state.message}
                    </Alert>
                </Snackbar>
                <div className="rechargecalc">
                    <div className="main">
                        <div className="money">{this.formatNumber(this.state.showCoin)}￥</div>
                        <div className="content">
                            <p className="title">{this.formatNumber(this.state.showCoin * this.state.totalNumber)}元</p>
                            <div className="goods_para">
                                <dl>
                                    <dt>您的购买价：</dt>
                                    <dd><strong className="goods_pice">{this.formatNumber(this.state.totalPrice)}</strong>豆</dd>
                                </dl>
                            </div>
                            <div className="goods_buy">
                                <div className="goods_buy_t">购买数量：</div>
                                <ButtonGroup variant="contained" color="primary" aria-label="contained primary button group">
                                    <Button onClick={this.onMin}>-</Button>
                                    <Button>{this.state.totalNumber}</Button>
                                    <Button onClick={this.onAdd}>+</Button>
                                </ButtonGroup>
                            </div>
                            <div className="goods_buy">
                                <div className="goods_buy_t">安全密码：</div>
                                <TextField variant="outlined" placeholder="请输入安全密码" type="password" onChange={(e) => this.passwordChange(e)} />
                            </div>
                            <Button className="buyButton" variant="contained" onClick={this.onSubmit}>立即兑换</Button>
                        </div>
                    </div>
                </div>
                <div className="recharge">
                    <p className="shopheader">您可能喜欢</p>
                    <div className="recharge-row">
                        <div className="price">
                            <a onClick={() => this.onChange(1)} className="p_header">50,000,000豆</a>
                            <p className="p_title">50,000￥</p>
                            <a onClick={() => this.onChange(1)}>立即兑换</a>
                        </div>
                        <div className="price">
                            <a onClick={() => this.onChange(2)} className="p_header">10,000,000豆</a>
                            <p className="p_title">10,000￥</p>
                            <a onClick={() => this.onChange(2)}>立即兑换</a>
                        </div>
                        <div className="price">
                            <a onClick={() => this.onChange(3)} className="p_header">5,000,000豆</a>
                            <p className="p_title">5,000￥</p>
                            <a onClick={() => this.onChange(3)}>立即兑换</a>
                        </div>
                    </div>
                    <div className="recharge-row">
                        <div className="price">
                            <a onClick={() => this.onChange(4)} className="p_header">1,000,000豆</a>
                            <p className="p_title">1,000￥</p>
                            <a onClick={() => this.onChange(4)}>立即兑换</a>
                        </div>
                        <div className="price">
                            <a onClick={() => this.onChange(5)} className="p_header">500,000豆</a>
                            <p className="p_title">500￥</p>
                            <a onClick={() => this.onChange(5)}>立即兑换</a>
                        </div>
                        <div className="price">
                            <a onClick={() => this.onChange(6)} className="p_header">100,000豆</a>
                            <p className="p_title">100￥</p>
                            <a onClick={() => this.onChange(6)}>立即兑换</a>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default withRouter(WithdrawlCalc)