import React, { Component } from 'react'
import '../css/game_centre.css'
import { Tabs, Tab } from 'react-bootstrap'
import Game_information from './GameCp/Game_information'
import { withRouter } from 'react-router-dom'
import axios from 'axios'
import Auth from './Authentication/auth'
import urls from '../urls.json'

const PROFILE_URL = urls.BASE_URL + urls.PROFILE

class Game_centre extends Component {
    constructor(props) {
        super(props)
        this.setKey = this.setKey.bind(this)
        this.setCanadaKey = this.setCanadaKey.bind(this)
        this.setItalyKey = this.setItalyKey.bind(this)
        this.authTest = this.authTest.bind(this)
        this.state = {
            mounted: false,
            canadaKey: localStorage.getItem("canadaKey"),
            italyKey: localStorage.getItem("italyKey"),
            game_id: (localStorage.getItem("parentKey") === "Canada" ? localStorage.getItem("canadaKey") : localStorage.getItem("italyKey")),
        }
        this.game_information = React.createRef()
    }

    async componentDidMount() {
        localStorage.setItem('nKey', 3)
        this.authTest()
        
    }

    componentWillUnmount = () => {
        this.setState = (state, callback) => {
            return;
        };
    }

    async authTest() {
        axios.post(PROFILE_URL + "&token=" + Auth.getToken()).then(async (res) => {
            if (res.data.type === "needlogin" || res.data.then === "fail") {
                alert("您的登录状态无效，请重新登录。")
                await Auth.deauthenticateUser()
                window.location.reload()
            } else {
                localStorage.setItem("TotalAmount", res.data.topics.TotalAmount)
                if (this.game_information.current === null) return
                await this.game_information.current.reload()
            }
        }).catch(err => {
            console.log(err)
        })
    }

    async setKey(k) {
        await this.authTest()
        localStorage.setItem("parentKey", k)
        if (k === "Canada") await this.setState({ game_id: localStorage.getItem("canadaKey") })
        else await this.setState({ game_id: localStorage.getItem("italyKey") })
        this.props.history.push("/regame_centre")
    }
    
    async setCanadaKey(k) {
        localStorage.setItem("canadaKey", k)
        await this.authTest()
        await this.setState({ canadaKey: k, game_id: k })
        this.props.history.push("/regame_centre")
    }

    async setItalyKey(k) {
        localStorage.setItem("italyKey", k)
        await this.authTest()
        await this.setState({ italyKey: k, game_id: k })
        this.props.history.push("/regame_centre")
    }

    render() {
        return (
            <div className="game_centre">
                <Tabs defaultActiveKey={localStorage.getItem("parentKey")} transition={false} className="parent" onSelect={this.setKey}>
                    <Tab eventKey="Canada" title="加拿大系列">
                        <Tabs defaultActiveKey={localStorage.getItem("canadaKey")} transition={false} className="parent" onSelect={this.setCanadaKey} activeKey={this.canadaKey}>
                            <Tab eventKey="canada_11" title="加拿大11"></Tab>
                            <Tab eventKey="canada_16" title="加拿大16" ></Tab>
                            <Tab eventKey="canada_28" title="加拿大28" ></Tab>
                            <Tab eventKey="canada_36" title="加拿大36" ></Tab>
                            <Tab eventKey="canada_dw" title="加拿大定位" ></Tab>
                            <Tab eventKey="canada_lhb" title="加拿大龙虎豹" ></Tab>
                        </Tabs>
                    </Tab>
                    <Tab eventKey="Italy" title="意大利系列">
                        <Tabs defaultActiveKey={localStorage.getItem("italyKey")} transition={false} className="parent" onSelect={this.setItalyKey} activeKey={this.italyKey}>
                            <Tab eventKey="italy_11" title="意大利11"></Tab>
                            <Tab eventKey="italy_16" title="意大利16" ></Tab>
                            <Tab eventKey="italy_28" title="意大利28" ></Tab>
                            <Tab eventKey="italy_36" title="意大利36" ></Tab>
                            <Tab eventKey="italy_dw" title="意大利定位" ></Tab>
                            <Tab eventKey="italy_lhb" title="意大利龙虎豹"></Tab>
                        </Tabs>
                    </Tab>
                </Tabs>
                <Game_information ref={this.game_information} Game_id={this.state.game_id} sumvalue={360} />
            </div>
        )
    }
}

export default withRouter(Game_centre)